<template>
  <div class="puzzle-container">
    <!-- Botn para activar/desactivar modo edicin -->
    <button class="boton-edicion" @click="toggleModoEdicion">
      {{ modoEdicion ? ' Salir Edicin' : ' Editar Velas' }}
    </button>

    <div class="game">
      <div class="background">
        <!-- Fondo del Cristo con opacidad -->
        <img
          :src="require('@/assets/puzzle_santo_cristo/fondo_cristo.png')"
          class="fondo"
          :style="{ 
            filter: `brightness(${iluminacion})`,
            opacity: 0.7
          }"
          alt="Fondo Santo Cristo"
        />

        <!-- CANDELABRO - NO EDITABLE -->
        <div
          class="candelabro-fijo"
          :style="{
            left: elementos.candelabro.x + 'px',
            top: elementos.candelabro.y + 'px',
            transform: `scale(${elementos.candelabro.escala})`
          }"
        >
          <img 
            :src="require('@/assets/puzzle_santo_cristo/Candelabro.png')" 
            class="candelabro-img"
            :style="{
              width: elementos.candelabro.ancho + 'px'
            }"
            alt="Candelabro"
          />
        </div>

        <!-- VITRINA - NO EDITABLE -->
        <div
          class="vitrina-fija"
          :style="{
            left: elementos.vitrina.x + 'px',
            top: elementos.vitrina.y + 'px',
            transform: `scale(${elementos.vitrina.escala})`
          }"
        >
          <img 
            :src="require('@/assets/puzzle_santo_cristo/vitrina.png')" 
            class="vitrina-img"
            :style="{
              width: elementos.vitrina.ancho + 'px'
            }"
            alt="Vitrina"
          />
        </div>

        <!-- VELAS - 8 VELAS EDITABLES (SIN VELA9) -->
        <div class="velas">
          <div
            v-for="(vela, i) in velas"
            :key="i"
            class="vela-editable"
            :class="{ 'seleccionado': elementoSeleccionado === `vela${i}`, 'arrastrando': arrastrando }"
            :style="{
              left: elementos[`vela${i}`]?.x + 'px',
              top: elementos[`vela${i}`]?.y + 'px',
              transform: `scale(${elementos[`vela${i}`]?.escala || 1})`,
              zIndex: 1000
            }"
            @mousedown="iniciarArrastre($event, `vela${i}`)"
            @click="!modoEdicion && seleccionarVela(i)"
          >
            <img
              :src="obtenerFrameVela(i)"
              class="vela-img"
              :style="{ 
                opacity: vela.encendida ? 1 : 0.4,
                width: (elementos[`vela${i}`]?.ancho || 60) + 'px'
              }"
              :alt="`Vela ${i + 1}`"
            />
            
            <!-- Indicador de seleccin muy visible -->
            <div v-if="modoEdicion && elementoSeleccionado === `vela${i}`" class="indicador-seleccion">
               VELA {{ i + 1 }} SELECCIONADA
            </div>
          </div>
        </div>
      </div>

      <!-- CONTROLES -->
      <div class="controls">
        <button v-if="!jugando && !modoEdicion" @click="iniciarJuego" class="boton-iniciar">
          Iniciar Juego
        </button>
        <p class="nivel">Nivel: {{ nivel }}</p>
        <p class="puntaje">Puntaje: {{ puntaje }}</p>
        <p class="mensaje">{{ mensaje }}</p>
        
        <!-- PANEL DE EDICIN -->
        <div v-if="modoEdicion" class="panel-edicion">
          <h3> EDITANDO VELAS</h3>
          
          <div class="controles-globales">
            <button @click="guardarPosiciones" class="btn-guardar"> GUARDAR</button>
            <button @click="resetearPosiciones" class="btn-reset"> RESETEAR VELAS</button>
          </div>

          <div v-if="elementoSeleccionado" class="controles-elemento">
            <h4>Editando: {{ obtenerNombreElemento(elementoSeleccionado) }}</h4>
            
            <div class="control-grupo">
              <label>TAMAO:</label>
              <div class="botones-control">
                <button @click="cambiarEscala(-0.1, elementoSeleccionado)"></button>
                <span class="valor">{{ Math.round((elementos[elementoSeleccionado]?.escala || 1) * 100) }}%</span>
                <button @click="cambiarEscala(0.1, elementoSeleccionado)"></button>
              </div>
            </div>

            <div class="control-grupo">
              <label>POSICIN:</label>
              <div class="coordenadas">
                X: {{ Math.round(elementos[elementoSeleccionado]?.x || 0) }}px | 
                Y: {{ Math.round(elementos[elementoSeleccionado]?.y || 0) }}px
              </div>
            </div>

            <div class="control-grupo">
              <label>ANCHO:</label>
              <div class="botones-control">
                <button @click="cambiarAncho(-5, elementoSeleccionado)"></button>
                <span class="valor">{{ elementos[elementoSeleccionado]?.ancho || 60 }}px</span>
                <button @click="cambiarAncho(5, elementoSeleccionado)"></button>
              </div>
            </div>
          </div>

          <div v-else class="instrucciones">
            <p> <strong>Haz clic en cualquier vela para seleccionarla</strong></p>
            <p> <strong>Arrastra para mover las velas</strong></p>
            <p> <strong>Usa los botones para cambiar tamao</strong></p>
            <p> <strong>Las velas estn POR ENCIMA de todo</strong></p>
            <p> <strong>8 velas editables</strong></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PuzzleSantoCristo',
  data() {
    // Tus posiciones actuales del candelabro y vitrina (FIJAS)
    const elementosFijos = {
      candelabro: { x: 400, y: 237.1875, ancho: 710, escala: 1.8 },
      vitrina: { x: 505, y: 297.3125228881836, ancho: 500, escala: 2.6 }
    };

    // TUS 8 VELAS ACTUALES (EDITABLES) - SIN VELA9
    const velasEditables = {
      vela0: { x: 1030, y: 303, ancho: 60, escala: 3 },
      vela1: { x: 573, y: 385, ancho: 60, escala: 3 },
      vela2: { x: 693, y: 309, ancho: 60, escala: 3 },
      vela3: { x: 898, y: 385, ancho: 60, escala: 3 },
      vela4: { x: 423, y: 310, ancho: 60, escala: 3 },
      vela5: { x: 762, y: 446, ancho: 60, escala: 3 },
      vela6: { x: 444, y: 462, ancho: 60, escala: 3 },
      vela7: { x: 1074, y: 463, ancho: 60, escala: 3 }
      // vela8 ELIMINADA
    };

    return {
      // AHORA 8 VELAS (SIN VELA9)
      velas: new Array(8).fill().map(() => ({ 
        encendida: false, 
        frame: 1,
        animando: false 
      })),
      secuencia: [],
      entradaJugador: [],
      nivel: 0,
      puntaje: 0,
      jugando: false,
      puedeTocar: false,
      iluminacion: 0.4,
      mensaje: "",
      puzzleDesbloqueado: false,
      intervaloAnimacion: null,
      
      // Sistema de edicin
      modoEdicion: false,
      elementoSeleccionado: null,
      arrastrando: false,
      offsetX: 0,
      offsetY: 0,
      
      // Combinar todas las posiciones (solo velas son editables)
      elementos: {
        ...elementosFijos,
        ...velasEditables
      }
    }
  },
  mounted() {
    this.iniciarAnimacionVelas()
    this.cargarPosicionesGuardadas()
    this.configurarEventosGlobales()
  },
  beforeUnmount() {
    if (this.intervaloAnimacion) {
      clearInterval(this.intervaloAnimacion)
    }
    this.removerEventosGlobales()
  },
  methods: {
    // ========== SISTEMA DE EDICIN ==========
    toggleModoEdicion() {
      this.modoEdicion = !this.modoEdicion
      this.elementoSeleccionado = null
      this.arrastrando = false
      if (!this.modoEdicion) {
        this.guardarPosiciones()
      }
    },

    configurarEventosGlobales() {
      document.addEventListener('mousemove', this.moverElemento)
      document.addEventListener('mouseup', this.detenerArrastre)
    },

    removerEventosGlobales() {
      document.removeEventListener('mousemove', this.moverElemento)
      document.removeEventListener('mouseup', this.detenerArrastre)
    },

    iniciarArrastre(event, elemento) {
      if (!this.modoEdicion) return
      
      // Solo permitir arrastre de velas (elementos que empiezan con "vela")
      if (!elemento.startsWith('vela')) return
      
      console.log('INICIANDO ARRASTRE:', elemento)
      
      event.preventDefault()
      event.stopPropagation()
      
      this.arrastrando = true
      this.elementoSeleccionado = elemento
      
      // Calcular offset desde la esquina del contenedor
      const rect = event.currentTarget.getBoundingClientRect()
      this.offsetX = event.clientX - rect.left
      this.offsetY = event.clientY - rect.top
      
      console.log('Offset:', this.offsetX, this.offsetY)
      console.log('Posicin inicial:', this.elementos[elemento].x, this.elementos[elemento].y)
    },

    moverElemento(event) {
      if (!this.arrastrando || !this.elementoSeleccionado) return
      
      // Solo permitir mover velas
      if (!this.elementoSeleccionado.startsWith('vela')) return
      
      const elemento = this.elementos[this.elementoSeleccionado]
      if (!elemento) return
      
      // Calcular nueva posicin restando el offset
      const newX = event.clientX - this.offsetX
      const newY = event.clientY - this.offsetY
      
      // Actualizar posicin
      elemento.x = newX
      elemento.y = newY
      
      console.log('Moviendo a:', newX, newY)
    },

    detenerArrastre() {
      console.log('DETENIENDO ARRASTRE')
      this.arrastrando = false
    },

    cambiarEscala(cantidad, elementoKey) {
      // Solo permitir cambiar escala de velas
      if (!elementoKey.startsWith('vela')) return
      
      const elemento = this.elementos[elementoKey]
      if (!elemento) return
      
      const nuevaEscala = elemento.escala + cantidad
      if (nuevaEscala >= 0.3 && nuevaEscala <= 3) {
        elemento.escala = Math.round(nuevaEscala * 10) / 10
      }
    },

    cambiarAncho(cantidad, elementoKey) {
      // Solo permitir cambiar ancho de velas
      if (!elementoKey.startsWith('vela')) return
      
      const elemento = this.elementos[elementoKey]
      if (!elemento) return
      
      const nuevoAncho = elemento.ancho + cantidad
      if (nuevoAncho >= 20 && nuevoAncho <= 200) {
        elemento.ancho = nuevoAncho
      }
    },

    obtenerNombreElemento(key) {
      const nombres = {
        vela0: 'VELA 1',
        vela1: 'VELA 2', 
        vela2: 'VELA 3',
        vela3: 'VELA 4',
        vela4: 'VELA 5',
        vela5: 'VELA 6',
        vela6: 'VELA 7',
        vela7: 'VELA 8'
        // vela8 eliminada
      }
      return nombres[key] || key
    },

    guardarPosiciones() {
      // Solo guardar las velas (elementos editables)
      const posicionesAGuardar = {}
      Object.keys(this.elementos).forEach(key => {
        if (key.startsWith('vela')) {
          posicionesAGuardar[key] = this.elementos[key]
        }
      })
      
      localStorage.setItem('puzzlePosiciones', JSON.stringify(posicionesAGuardar))
      this.mensaje = " POSICIONES GUARDADAS"
      setTimeout(() => {
        if (!this.jugando) this.mensaje = ""
      }, 2000)
    },

    cargarPosicionesGuardadas() {
      const posicionesGuardadas = localStorage.getItem('puzzlePosiciones')
      if (posicionesGuardadas) {
        try {
          const posicionesCargadas = JSON.parse(posicionesGuardadas)
          
          // Solo cargar posiciones de velas (elementos editables)
          Object.keys(posicionesCargadas).forEach(key => {
            if (key.startsWith('vela') && this.elementos[key]) {
              this.elementos[key] = { ...this.elementos[key], ...posicionesCargadas[key] }
            }
          })
          
        } catch (error) {
          console.error('Error al cargar posiciones:', error)
          // Si hay error, usar posiciones por defecto para velas
          this.resetearPosicionesVelas()
        }
      }
    },

    resetearPosiciones() {
      // Solo resetear las velas (elementos editables)
      this.resetearPosicionesVelas()
      this.mensaje = " POSICIONES DE VELAS RESETEADAS"
      setTimeout(() => {
        if (!this.jugando) this.mensaje = ""
      }, 2000)
    },

    resetearPosicionesVelas() {
      // TUS POSICIONES EXACTAS PARA LAS 8 VELAS
      const posicionesVelasPorDefecto = {
        vela0: { x: 1030, y: 303, ancho: 60, escala: 3 },
        vela1: { x: 573, y: 385, ancho: 60, escala: 3 },
        vela2: { x: 693, y: 309, ancho: 60, escala: 3 },
        vela3: { x: 898, y: 385, ancho: 60, escala: 3 },
        vela4: { x: 423, y: 310, ancho: 60, escala: 3 },
        vela5: { x: 762, y: 446, ancho: 60, escala: 3 },
        vela6: { x: 444, y: 462, ancho: 60, escala: 3 },
        vela7: { x: 1074, y: 463, ancho: 60, escala: 3 }
        // vela8 eliminada
      }
      
      // Aplicar solo a las velas
      Object.keys(posicionesVelasPorDefecto).forEach(key => {
        if (this.elementos[key]) {
          this.elementos[key] = { ...posicionesVelasPorDefecto[key] }
        }
      })
    },

    // ========== SISTEMA DE ANIMACIN Y JUEGO ==========
    iniciarAnimacionVelas() {
      this.intervaloAnimacion = setInterval(() => {
        this.velas.forEach((vela) => {
          if (!vela.animando && !vela.encendida) {
            vela.frame = 1
          }
        })
      }, 1000 / 18)
    },

    obtenerFrameVela(index) {
      const vela = this.velas[index]
      const frameNum = vela.frame.toString().padStart(2, '0')
      try {
        return require(`@/assets/puzzle_santo_cristo/animacion_vela/vela${frameNum}.png`)
      } catch (error) {
        console.log(`Frame vela${frameNum}.png no encontrado, usando vela01.png`)
        try {
          return require('@/assets/puzzle_santo_cristo/animacion_vela/vela01.png')
        } catch (e) {
          console.log('No se pudo cargar ningn frame de vela')
          return ''
        }
      }
    },

    async animarVela(index, encender = true) {
      const vela = this.velas[index]
      vela.animando = true
      
      if (encender) {
        for (let frame = 1; frame <= 24; frame++) {
          vela.frame = frame
          await this.esperar(1000 / 18)
        }
        vela.encendida = true
        vela.frame = 24
      } else {
        for (let frame = 24; frame >= 1; frame--) {
          vela.frame = frame
          await this.esperar(1000 / 18)
        }
        vela.encendida = false
        vela.frame = 1
      }
      
      vela.animando = false
    },

    iniciarJuego() {
      this.secuencia = []
      this.nivel = 0
      this.puntaje = 0
      this.puzzleDesbloqueado = false
      this.iluminacion = 0.4
      this.mensaje = ""
      this.jugando = true
      this.siguienteRonda()
    },

    siguienteRonda() {
      this.nivel++
      this.entradaJugador = []
      // Ahora el juego puede usar cualquiera de las 8 velas
      this.secuencia.push(Math.floor(Math.random() * this.velas.length))
      this.mostrarSecuencia()
    },

    async mostrarSecuencia() {
      this.puedeTocar = false
      for (let i = 0; i < this.secuencia.length; i++) {
        const index = this.secuencia[i]
        await this.animarVela(index, true)
        await this.esperar(600)
        await this.animarVela(index, false)
        await this.esperar(200)
      }
      this.puedeTocar = true
    },

    async seleccionarVela(index) {
      if (!this.puedeTocar || this.velas[index].animando || this.modoEdicion) return
      
      await this.animarVela(index, true)
      this.entradaJugador.push(index)
      setTimeout(async () => {
        await this.animarVela(index, false)
        this.verificarEntrada()
      }, 400)
    },

    verificarEntrada() {
      const paso = this.entradaJugador.length - 1
      if (this.entradaJugador[paso] !== this.secuencia[paso]) {
        this.mensaje = " Fallaste. Intenta de nuevo."
        this.reiniciarRonda()
        return
      }

      if (this.entradaJugador.length === this.secuencia.length) {
        this.puntaje += this.nivel * 10
        this.mensaje = " Correcto. Avanzas de nivel."
        this.iluminacion = Math.min(this.iluminacion + 0.02, 0.8)
        
        setTimeout(() => {
          if (this.nivel >= 5 && !this.puzzleDesbloqueado) {
            this.puzzleCompletado()
            this.puzzleDesbloqueado = true
            this.mensaje = " Puzzle Desbloqueado!  - Contina jugando..."
          }
          this.siguienteRonda()
        }, 1000)
      }
    },

    async reiniciarRonda() {
      this.puedeTocar = false
      for (let i = 0; i < this.velas.length; i++) {
        if (this.velas[i].encendida) {
          await this.animarVela(i, false)
        }
      }
      
      setTimeout(() => {
        this.mensaje = ""
        this.entradaJugador = []
        this.mostrarSecuencia()
      }, 1500)
    },

    esperar(ms) {
      return new Promise((r) => setTimeout(r, ms))
    },

    puzzleCompletado() {
      this.$emit('completado')
    }
  }
}
</script>

<style scoped>
/* Los estilos se mantienen igual que en la versin anterior */
.puzzle-container {
  position: relative;
  width: 100%;
  height: 100vh;
  user-select: none;
  overflow: hidden;
}

.boton-edicion {
  position: absolute;
  top: 20px;
  right: 20px;
  background: rgba(0, 0, 0, 0.9);
  color: white;
  border: 2px solid #ffd54f;
  border-radius: 8px;
  padding: 12px 16px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  z-index: 10000;
  transition: all 0.3s ease;
}

.boton-edicion:hover {
  background: #ffd54f;
  color: black;
  transform: scale(1.05);
}

.game {
  text-align: center;
  color: white;
  font-family: 'Poppins', sans-serif;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.background {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.fondo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: filter 0.6s ease;
  z-index: 1;
}

.candelabro-fijo, .vitrina-fija {
  position: absolute;
  cursor: default;
  z-index: 10;
}

.vela-editable {
  position: absolute;
  cursor: grab;
  transition: all 0.2s ease;
  z-index: 1000 !important;
}

.vela-editable:hover {
  outline: 3px dashed #ffd54f;
  z-index: 1001 !important;
}

.vela-editable.seleccionado {
  outline: 3px solid #4CAF50;
  z-index: 1002 !important;
}

.vela-editable.arrastrando {
  cursor: grabbing;
  outline: 3px solid #ff6b6b;
  filter: brightness(1.1);
  z-index: 1003 !important;
}

.candelabro-img, .vitrina-img, .vela-img {
  width: 100%;
  height: auto;
  display: block;
  pointer-events: none;
}

.candelabro-fijo {
  z-index: 10;
}

.vitrina-fija {
  z-index: 20;
}

.indicador-seleccion {
  position: absolute;
  top: -35px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255, 215, 0, 0.95);
  color: black;
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 12px;
  font-weight: bold;
  white-space: nowrap;
  z-index: 1004;
  border: 2px solid #ff6b00;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { transform: translateX(-50%) scale(1); }
  50% { transform: translateX(-50%) scale(1.05); }
  100% { transform: translateX(-50%) scale(1); }
}

.controls {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
  z-index: 2000;
  background: rgba(0, 0, 0, 0.8);
  padding: 20px;
  border-radius: 15px;
  border: 2px solid #333;
  min-width: 300px;
}

.boton-iniciar {
  background-color: #ffd54f;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-weight: bold;
  cursor: pointer;
  color: #3e2723;
  font-size: 16px;
  transition: all 0.3s;
  margin-bottom: 10px;
}

.boton-iniciar:hover {
  background-color: #ffb300;
  transform: scale(1.05);
}

.nivel {
  font-size: 16px;
  font-weight: bold;
  margin: 5px 0;
}

.puntaje {
  font-size: 16px;
  font-weight: bold;
  margin: 5px 0;
  color: #ffd54f;
}

.mensaje {
  font-size: 16px;
  margin-top: 10px;
  text-shadow: 0 0 10px #000;
  font-weight: bold;
  min-height: 24px;
}

.panel-edicion {
  margin-top: 15px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.95);
  border-radius: 12px;
  border: 3px solid #ffd54f;
}

.panel-edicion h3 {
  margin: 0 0 15px 0;
  font-size: 16px;
  color: #ffd54f;
  text-align: center;
  text-transform: uppercase;
}

.controles-globales {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-bottom: 20px;
}

.btn-guardar, .btn-reset {
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 10px 15px;
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
  flex: 1;
}

.btn-reset {
  background: #f44336;
}

.btn-guardar:hover, .btn-reset:hover {
  transform: scale(1.05);
}

.controles-elemento {
  background: rgba(255, 213, 79, 0.1);
  padding: 15px;
  border-radius: 8px;
  border: 1px solid #ffd54f;
  margin-bottom: 15px;
}

.controles-elemento h4 {
  margin: 0 0 15px 0;
  color: #ffd54f;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
}

.control-grupo {
  margin-bottom: 12px;
