<template>
  <div class="mercado-container">
    <!-- Fondo del mercado -->
    <div class="fondo-mercado"></div>
    
    <!-- Botn para volver -->
    <button class="btn-volver" @click="volverAInicio">
       Volver al Inicio
    </button>
    
    <!-- Ttulo del mercado -->
    <div class="titulo-mercado">
      <h1> Mercado Ancestral</h1>
      <p class="subtitulo">Descubre productos exclusivos de Lumbral</p>
    </div>
    
    <!-- Saldo del jugador -->
    <div class="saldo-jugador">
      <div class="saldo-info">
        <span class="icono-moneda"></span>
        <span class="cantidad-saldo">{{ formatoMoneda(saldo) }}</span>
      </div>
      <button class="btn-recargar" @click="recargarSaldo">+ Recargar</button>
    </div>
    
    <!-- Layout principal -->
    <div class="layout-mercado">
      <!-- Men lateral -->
      <div class="menu-lateral">
        <div 
          v-for="(categoria, index) in categorias" 
          :key="index"
          class="item-menu"
          :class="{ activo: categoriaActiva === index }"
          @click="seleccionarCategoria(index)"
        >
          <span class="icono-categoria">{{ categoria.icono }}</span>
          <span class="texto-categoria">{{ categoria.nombre }}</span>
          <span v-if="categoria.nuevo" class="badge-nuevo">NUEVO</span>
        </div>
      </div>
      
      <!-- Contenido principal -->
      <div class="contenido-principal">
        <!-- Ttulo de categora -->
        <div class="cabecera-categoria">
          <h2>{{ categorias[categoriaActiva].nombre }}</h2>
          <p class="descripcion-categoria">{{ categorias[categoriaActiva].descripcion }}</p>
        </div>
        
        <!-- Carrusel de productos -->
        <div class="carrusel-container">
          <!-- Flecha izquierda -->
          <button class="flecha-carrusel izquierda" @click="anteriorProducto" :disabled="indiceProducto === 0">
            
          </button>
          
          <!-- Producto actual -->
          <div class="producto-actual">
            <div class="imagen-producto-container">
              <img 
                :src="productoActual.imagen" 
                :alt="productoActual.nombre"
                class="imagen-producto"
              />
              <div v-if="productoActual.descuento" class="badge-descuento">
                -{{ productoActual.descuento }}%
              </div>
              <div v-if="productoActual.nuevo" class="badge-producto-nuevo">
                NUEVO
              </div>
            </div>
            
            <div class="info-producto">
              <h3 class="nombre-producto">{{ productoActual.nombre }}</h3>
              <p class="descripcion-producto">{{ productoActual.descripcion }}</p>
              
              <div class="detalles-producto">
                <div class="detalle">
                  <span class="icono-detalle"></span>
                  <span>Rareza: {{ productoActual.rareza }}</span>
                </div>
                <div class="detalle">
                  <span class="icono-detalle"></span>
                  <span>Stock: {{ productoActual.stock }}</span>
                </div>
                <div v-if="productoActual.duracion" class="detalle">
                  <span class="icono-detalle"></span>
                  <span>Duracin: {{ productoActual.duracion }}</span>
                </div>
              </div>
              
              <div class="precio-container">
                <div class="precio-original" v-if="productoActual.precioOriginal">
                  {{ formatoMoneda(productoActual.precioOriginal) }}
                </div>
                <div class="precio-actual">
                  <span class="icono-moneda"></span>
                  <span class="cantidad-precio">{{ formatoMoneda(productoActual.precio) }}</span>
                </div>
              </div>
              
              <div class="acciones-producto">
                <button 
                  class="btn-comprar"
                  @click="comprarProducto"
                  :disabled="productoActual.stock === 0 || saldo < productoActual.precio"
                >
                  {{ productoActual.stock === 0 ? 'AGOTADO' : 'COMPRAR AHORA' }}
                </button>
                <button class="btn-detalles" @click="verDetalles">
                  VER DETALLES
                </button>
              </div>
            </div>
          </div>
          
          <!-- Flecha derecha -->
          <button class="flecha-carrusel derecha" @click="siguienteProducto" :disabled="indiceProducto === productosCategoria.length - 1">
            
          </button>
        </div>
        
        <!-- Indicadores del carrusel -->
        <div class="indicadores-carrusel">
          <div 
            v-for="(producto, index) in productosCategoria" 
            :key="index"
            class="indicador"
            :class="{ activo: indiceProducto === index }"
            @click="irAProducto(index)"
          ></div>
        </div>
        
        <!-- Productos relacionados -->
        <div v-if="productosRelacionados.length > 0" class="productos-relacionados">
          <h3>Productos relacionados</h3>
          <div class="grid-relacionados">
            <div 
              v-for="(producto, index) in productosRelacionados" 
              :key="index"
              class="card-relacionado"
              @click="seleccionarProductoRelacionado(producto)"
            >
              <img :src="producto.imagen" :alt="producto.nombre" />
              <div class="info-relacionado">
                <h4>{{ producto.nombre }}</h4>
                <div class="precio-relacionado">
                  <span class="icono-moneda-pequeno"></span>
                  {{ formatoMoneda(producto.precio) }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Modal de detalles -->
    <div v-if="modalVisible" class="modal-overlay" @click.self="cerrarModal">
      <div class="modal-contenido">
        <button class="btn-cerrar-modal" @click="cerrarModal"></button>
        <h2>{{ productoSeleccionadoModal.nombre }}</h2>
        <div class="modal-cuerpo">
          <img :src="productoSeleccionadoModal.imagen" alt="" class="modal-imagen" />
          <div class="modal-info">
            <p>{{ productoSeleccionadoModal.descripcionLarga || productoSeleccionadoModal.descripcion }}</p>
            
            <div class="caracteristicas">
              <h4>Caractersticas:</h4>
              <ul>
                <li v-for="(caracteristica, index) in productoSeleccionadoModal.caracteristicas" :key="index">
                  {{ caracteristica }}
                </li>
              </ul>
            </div>
            
            <div class="modal-precio">
              <div class="precio-final">
                <span class="icono-moneda"></span>
                <span class="cantidad">{{ formatoMoneda(productoSeleccionadoModal.precio) }}</span>
              </div>
              <button 
                class="btn-comprar-modal"
                @click="comprarDesdeModal"
                :disabled="productoSeleccionadoModal.stock === 0 || saldo < productoSeleccionadoModal.precio"
              >
                {{ productoSeleccionadoModal.stock === 0 ? 'AGOTADO' : 'COMPRAR PRODUCTO' }}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Notificaciones -->
    <div v-if="notificacionVisible" class="notificacion" :class="notificacionTipo">
      {{ notificacionMensaje }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'MercadoJuego',
  data() {
    return {
      categoriaActiva: 0,
      indiceProducto: 0,
      modalVisible: false,
      productoSeleccionadoModal: null,
      saldo: 5000,
      notificacionVisible: false,
      notificacionMensaje: '',
      notificacionTipo: 'exito',
      
      categorias: [
        { 
          nombre: 'Paquetes Tursticos', 
          icono: '',
          descripcion: 'Experiencias nicas en Lumbral',
          nuevo: true
        },
        { 
          nombre: 'Personajes', 
          icono: '',
          descripcion: 'Desbloquea personajes histricos',
          nuevo: false
        },
        { 
          nombre: 'Mercanca', 
          icono: '',
          descripcion: 'Productos exclusivos del juego',
          nuevo: false
        },
        { 
          nombre: 'Mapas Narrativos', 
          icono: '',
          descripcion: 'Historias nicas para explorar',
          nuevo: true
        }
      ],
      
      productos: {
        // Paquetes Tursticos
        0: [
          {
            id: 1,
            nombre: 'Tour Histrico Completo',
            descripcion: 'Recorrido guiado por todos los sitios histricos de Lumbral',
            descripcionLarga: 'Un recorrido de 4 horas que incluye visita a 10 sitios histricos, gua especializado, y materiales educativos.',
            imagen: require('@/assets/mercado/tour_historico.jpg'),
            precio: 1500,
            precioOriginal: 2000,
            descuento: 25,
            stock: 15,
            rareza: 'Comn',
            duracion: '4 horas',
            caracteristicas: [
              'Gua especializado en historia',
              'Material educativo incluido',
              'Transporte entre sitios',
              'Fotografas profesionales',
              'Snack tradicional incluido'
            ],
            nuevo: true
          },
          {
            id: 2,
            nombre: 'Experiencia Nocturna Mstica',
            descripcion: 'Tour nocturno con leyendas y misterios de Lumbral',
            imagen: require('@/assets/mercado/tour_nocturno.jpg'),
            precio: 2000,
            stock: 8,
            rareza: 'Raro',
            duracion: '3 horas',
            caracteristicas: [
              'Gua contador de historias',
              'Linternas especiales',
              'Parada en sitios misteriosos',
              'Refrigerio nocturno',
              'Souvenir exclusivo'
            ],
            nuevo: false
          },
          {
            id: 3,
            nombre: 'Pack Familiar Weekend',
            descripcion: 'Experiencia completa para toda la familia durante un fin de semana',
            imagen: require('@/assets/mercado/pack_familiar.jpg'),
            precio: 3500,
            precioOriginal: 4500,
            descuento: 22,
            stock: 5,
            rareza: 'pico',
            duracion: '2 das',
            caracteristicas: [
              'Alojamiento en posada tradicional',
              'Todas las comidas incluidas',
              'Actividades para nios',
              'Talleres artesanales',
              'Fotos familiares profesionales'
            ],
            nuevo: true
          }
        ],
        
        // Personajes
        1: [
          {
            id: 4,
            nombre: 'Don Francisco de Miranda',
            descripcion: 'Personaje histrico con narrativa exclusiva',
            imagen: require('@/assets/mercado/personaje_miranda.jpg'),
            precio: 3000,
            stock: 3,
            rareza: 'Legendario',
            caracteristicas: [
              'Narrativa exclusiva de 5 captulos',
              'Habilidades especiales en puzzles',
              'Acceso a reas secretas',
              'Logros exclusivos',
              'Skin nico del personaje'
            ],
            nuevo: true
          },
          {
            id: 5,
            nombre: 'Sor Juana Ins',
            descripcion: 'Personaje literario con habilidades especiales',
            imagen: require('@/assets/mercado/personaje_sorjuana.jpg'),
            precio: 2500,
            stock: 7,
            rareza: 'pico',
            caracteristicas: [
              'Habilidad: Resolver acertijos rpido',
              'Bonus en misiones de conocimiento',
              'Dilogos exclusivos',
              'Coleccin de poemas nica',
              'Skin elegante de poca'
            ],
            nuevo: false
          }
        ],
        
        // Mercanca
        2: [
          {
            id: 6,
            nombre: 'Camiseta Coleccionista',
            descripcion: 'Camiseta oficial con diseo exclusivo de Susurros Ancestrales',
            imagen: require('@/assets/mercado/camiseta.jpg'),
            precio: 800,
            stock: 20,
            rareza: 'Comn',
            caracteristicas: [
              '100% algodn premium',
              'Diseo exclusivo bordado',
              'Edicin limitada numerada',
              'Incluye certificado de autenticidad',
              'Tallas S a XXL disponibles'
            ],
            nuevo: false
          },
          {
            id: 7,
            nombre: 'Figura Artesanal Lumbral',
            descripcion: 'Figura de cermica hecha a mano por artesanos locales',
            imagen: require('@/assets/mercado/figura.jpg'),
            precio: 1200,
            stock: 12,
            rareza: 'Raro',
            caracteristicas: [
              'Hecho a mano por artesanos',
              'Pintado con pigmentos naturales',
              'Base de madera incluida',
              'Certificado de autenticidad',
              'Edicin limitada a 100 unidades'
            ],
            nuevo: true
          }
        ],
        
        // Mapas Narrativos
        3: [
          {
            id: 8,
            nombre: 'Mapa del Tesoro Perdido',
            descripcion: 'Historia interactiva sobre el tesoro escondido de Lumbral',
            imagen: require('@/assets/mercado/mapa_tesoro.jpg'),
            precio: 1800,
            stock: 10,
            rareza: 'pico',
            caracteristicas: [
              'Narrativa de 3 horas de juego',
              '5 finales diferentes posibles',
              'Puzzles exclusivos',
              'Recompensas especiales',
              'Soundtrack original'
            ],
            nuevo: true
          },
          {
            id: 9,
            nombre: 'Crnica de la Revolucin',
            descripcion: 'Recorre los eventos histricos de forma interactiva',
            imagen: require('@/assets/mercado/mapa_revolucion.jpg'),
            precio: 2200,
            stock: 6,
            rareza: 'Legendario',
            caracteristicas: [
              'Recreacin histrica precisa',
              'Personajes histricos interactivos',
              'Decisiones que cambian la historia',
              'Logros coleccionables',
              'Modo multijugador disponible'
            ],
            nuevo: false
          }
        ]
      }
    };
  },
  computed: {
    productosCategoria() {
      return this.productos[this.categoriaActiva] || [];
    },
    productoActual() {
      return this.productosCategoria[this.indiceProducto] || {};
    },
    productosRelacionados() {
      return this.productosCategoria
        .filter((_, index) => index !== this.indiceProducto)
        .slice(0, 3);
    }
  },
  methods: {
    volverAInicio() {
      this.$emit('cambiar-pantalla', 'inicio');
    },
    
    seleccionarCategoria(index) {
      this.categoriaActiva = index;
      this.indiceProducto = 0;
    },
    
    anteriorProducto() {
      if (this.indiceProducto > 0) {
        this.indiceProducto--;
      }
    },
    
    siguienteProducto() {
      if (this.indiceProducto < this.productosCategoria.length - 1) {
        this.indiceProducto++;
      }
    },
    
    irAProducto(index) {
      this.indiceProducto = index;
    },
    
    comprarProducto() {
      const producto = this.productoActual;
      if (this.saldo >= producto.precio && producto.stock > 0) {
        this.saldo -= producto.precio;
        producto.stock--;
        
        this.mostrarNotificacion(
          `Compra exitosa! Has adquirido "${producto.nombre}"`,
          'exito'
        );
        
        // Aqu podras guardar la compra en localStorage
        this.guardarCompra(producto);
      } else if (producto.stock === 0) {
        this.mostrarNotificacion('Este producto est agotado', 'error');
      } else {
        this.mostrarNotificacion('Saldo insuficiente', 'error');
      }
    },
    
    verDetalles() {
      this.productoSeleccionadoModal = this.productoActual;
      this.modalVisible = true;
    },
    
    cerrarModal() {
      this.modalVisible = false;
    },
    
    comprarDesdeModal() {
      this.comprarProducto();
      this.cerrarModal();
    },
    
    seleccionarProductoRelacionado(producto) {
      const index = this.productosCategoria.findIndex(p => p.id === producto.id);
      if (index !== -1) {
        this.indiceProducto = index;
      }
    },
    
    recargarSaldo() {
      const recarga = 1000;
      this.saldo += recarga;
      this.mostrarNotificacion(
        `Recarga exitosa! +${this.formatoMoneda(recarga)}`,
        'exito'
      );
    },
    
    formatoMoneda(cantidad) {
      return cantidad.toLocaleString('es-CO', {
        minimumFractionDigits: 0,
        maximumFractionDigits: 0
      });
    },
    
    mostrarNotificacion(mensaje, tipo = 'exito') {
      this.notificacionMensaje = mensaje;
      this.notificacionTipo = tipo;
      this.notificacionVisible = true;
      
      setTimeout(() => {
        this.notificacionVisible = false;
      }, 3000);
    },
    
    guardarCompra(producto) {
      const compras = JSON.parse(localStorage.getItem('comprasMercado') || '[]');
      compras.push({
        id: producto.id,
        nombre: producto.nombre,
        fecha: new Date().toISOString(),
        precio: producto.precio
      });
      localStorage.setItem('comprasMercado', JSON.stringify(compras));
    }
  },
  mounted() {
    // Cargar saldo guardado
    const saldoGuardado = localStorage.getItem('saldoJugador');
    if (saldoGuardado) {
      this.saldo = parseInt(saldoGuardado);
    }
    
    // Definir manejador de teclas
    const handleKeydown = (e) => {
      if (e.key === 'ArrowLeft') {
        this.anteriorProducto();
      } else if (e.key === 'ArrowRight') {
        this.siguienteProducto();
      }
    };
    
    // Guardar referencia para poder removerlo despus
    this._handleKeydown = handleKeydown;
    
    // Agregar event listener
    document.addEventListener('keydown', handleKeydown);
  },
  // CORREGIDO: Cambiado beforeDestroy por beforeUnmount
  beforeUnmount() {
    // Guardar saldo
    localStorage.setItem('saldoJugador', this.saldo.toString());
    
    // Remover event listener
    if (this._handleKeydown) {
      document.removeEventListener('keydown', this._handleKeydown);
    }
  }
};
</script>

<style scoped>
.mercado-container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #0c2461 0%, #1e3799 50%, #4a69bd 100%);
  overflow-x: hidden;
  font-family: 'Arial', sans-serif;
  color: white;
}

.fondo-mercado {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.1) 0%, transparent 20%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.05) 0%, transparent 20%);
  z-index: 1;
}

.btn-volver {
  position: fixed;
  top: 20px;
  left: 20px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 10px 20px;
  border-radius: 25px;
  cursor: pointer;
  font-size: 14px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  z-index: 100;
}

.btn-volver:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(-5px);
}

.titulo-mercado {
  text-align: center;
  padding: 80px 20px 30px;
  z-index: 2;
  position: relative;
}

.titulo-mercado h1 {
  font-size: 3.5rem;
  margin-bottom: 10px;
  background: linear-gradient(45deg, #ff9ff3, #f368e0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.subtitulo {
  color: #a29bfe;
  font-size: 1.2rem;
  opacity: 0.9;
}

.saldo-jugador {
  position: fixed;
  top: 20px;
  right: 20px;
  background: rgba(0, 0, 0, 0.7);
  padding: 12px 20px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  gap: 15px;
  z-index: 100;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.saldo-info {
  display: flex;
  align-items: center;
  gap: 8px;
}

.icono-moneda {
  font-size: 1.2rem;
}

.cantidad-saldo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #fdcb6e;
}

.btn-recargar {
  background: linear-gradient(45deg, #00b894, #00cec9);
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.3s ease;
}

.btn-recargar:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 184, 148, 0.4);
}

.layout-mercado {
  display: flex;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 2;
}

/* Men lateral */
.menu-lateral {
  width: 250px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  padding: 20px;
  margin-right: 30px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  height: fit-content;
  position: sticky;
  top: 120px;
}

.item-menu {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.item-menu:hover {
  background: rgba(255, 255, 255, 0.1);
}

.item-menu.activo {
  background: linear-gradient(45deg, #6c5ce7, #a29bfe);
  box-shadow: 0 5px 15px rgba(108, 92, 231, 0.4);
}

.icono-categoria {
  font-size: 1.5rem;
}

.texto-categoria {
  font-size: 1rem;
  font-weight: 500;
  flex: 1;
}

.badge-nuevo {
  background: #e84393;
  color: white;
  font-size: 10px;
  padding: 3px 8px;
  border-radius: 10px;
  font-weight: bold;
}

/* Contenido principal */
.contenido-principal {
  flex: 1;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 25px;
  padding: 30px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.cabecera-categoria {
  margin-bottom: 30px;
  text-align: center;
}

.cabecera-categoria h2 {
  font-size: 2.2rem;
  margin-bottom: 10px;
  color: #74b9ff;
}

.descripcion-categoria {
  color: #dfe6e9;
  opacity: 0.8;
  font-size: 1.1rem;
}

/* Carrusel */
.carrusel-container {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
}

.flecha-carrusel {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 24px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.flecha-carrusel:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.2);
