<template>
  <div class="historia-container">
    <!-- Botn Volver -->
    <button class="boton-volver" @click="volver">
       VOLVER
    </button>

    <!-- Contenedor principal con scroll -->
    <div class="contenedor-scroll" ref="scrollContainer">
      
      <!-- Seccin 1: Portada -->
      <section class="seccion portada" id="portada">
        <div class="contenido-seccion">
          <!-- Imagen de portada - siempre intentar cargar -->
          <img 
            :src="portadaSrc" 
            class="imagen-portada"
            alt="Portada Santo Cristo"
            @load="imagenCargada.portada = true"
            @error="imagenCargada.portada = false"
          />
          <div class="placeholder-imagen" v-if="!imagenCargada.portada">
            <p> PORTADA DEL SANTO CRISTO </p>
            <p>Una leyenda de fe y renovacin</p>
          </div>
          
          <!-- Botn Podcast en la portada -->
          <div class="contenedor-podcast">
            <button class="boton-podcast" @click="toggleReproduccion">
              {{ reproduciendo ? ' Pausar Audio' : ' Escuchar Historia' }}
            </button>
            
            <!-- Barra de progreso del audio -->
            <div v-if="reproduciendo" class="barra-progreso">
              <div class="progreso" :style="{ width: progresoAudio + '%' }"></div>
              <div class="tiempo-actual">{{ tiempoActual }}</div>
            </div>
          </div>
        </div>
        
        <!-- Flecha indicadora para bajar -->
        <div class="flecha-bajar" @click="scrollToSeccion('puzzle')">
           Desliza para continuar 
        </div>
      </section>

      <!-- Seccin 2: Puzzle (pantalla completa) -->
      <section class="seccion puzzle pantalla-completa" id="puzzle">
        <div class="contenedor-puzzle-completo">
          <PuzzleSantoCristo 
            @completado="onPuzzleCompletado" 
            @volver="volverAHistoria" 
          />
        </div>
        
        <!-- Solo el botn para ver el final cuando est desbloqueado -->
        <div class="controles-puzzle" v-if="puzzleCompleto">
          <button 
            class="boton-ver-final" 
            @click="scrollToSeccion('final')"
          >
             Ver Recompensa 
          </button>
        </div>
      </section>

      <!-- Seccin 3: Final - CON IMAGEN Y BOTONES -->
      <section class="seccion final" id="final" :class="{ desbloqueado: puzzleCompleto }">
        <div class="contenido-final">
          
          <!-- Imagen Kroki -->
          <div class="contenedor-imagen-kroki">
            <img 
              :src="krokiSrc" 
              class="imagen-kroki"
              alt="Kroki del Santo Cristo"
              @load="imagenCargada.kroki = true"
              @error="imagenCargada.kroki = false"
            />
            <div class="placeholder-kroki" v-if="!imagenCargada.kroki">
              <p> Mapa Kroki</p>
              <p>Ruta del Santo Cristo</p>
            </div>
          </div>

          <!-- Mensaje del puzzle -->
          <div class="mensaje-recompensa">
            <h2> PUZZLE DEL CANDELABRO </h2>
            <p class="instrucciones">Completa el puzzle para desbloquear el final de la historia</p>
            
            <div class="estado-puzzle" v-if="!puzzleCompleto">
              <p class="mensaje-bloqueo"> Final bloqueado</p>
              <p class="sub-mensaje">Juega para revelar la recompensa!</p>
            </div>
            
            <div class="estado-puzzle" v-else>
              <p class="mensaje-desbloqueado"> Felicidades!</p>
              <p class="sub-mensaje">Has completado el puzzle y desbloqueado la historia</p>
              <p class="mensaje-ganador"> Donde hay fe, hay renovacin </p>
            </div>
          </div>
          
          <!-- Contenedor de botones -->
          <div class="contenedor-botones">
            <button class="boton-ruta" @click="irARuta">
               Conocer la Ruta
            </button>
            
            <button class="boton-volver-mapa" @click="volverAlMapa">
               Volver al Mapa Principal
            </button>
          </div>
        </div>
      </section>

    </div>

    <!-- Indicador de progreso lateral -->
    <div class="indicador-progreso">
      <div 
        class="punto" 
        :class="{ activo: seccionActual === 'portada' }"
        @click="scrollToSeccion('portada')"
      ></div>
      <div 
        class="punto" 
        :class="{ activo: seccionActual === 'puzzle' }"
        @click="scrollToSeccion('puzzle')"
      ></div>
      <div 
        class="punto" 
        :class="{ activo: seccionActual === 'final', desbloqueado: puzzleCompleto }"
        @click="puzzleCompleto && scrollToSeccion('final')"
      ></div>
    </div>
  </div>
</template>

<script>
import PuzzleSantoCristo from './puzzle_santo_cristo.vue'

export default {
  name: 'HistoriaSantoCristo',
  components: {
    PuzzleSantoCristo
  },
  data() {
    return {
      reproduciendo: false,
      audio: null,
      tiempoActual: '0:00',
      duracionAudio: '0:00',
      progresoAudio: 0,
      imagenCargada: {
        portada: false,
        kroki: false
      },
      puzzleCompleto: false,
      seccionActual: 'portada',
      portadaSrc: '',
      krokiSrc: ''
    }
  },
  mounted() {
    this.cargarImagenes()
    this.inicializarAudio()
    this.configurarScroll()
  },
  beforeUnmount() {
    if (this.audio) {
      this.audio.pause()
      this.audio = null
    }
  },
  methods: {
    cargarImagenes() {
      // Cargar portada
      try {
        this.portadaSrc = require('@/assets/historias/santo_cristo/portada.png')
        this.imagenCargada.portada = true
      } catch (e) {
        console.log('No se pudo cargar portada.png')
        this.portadaSrc = ''
        this.imagenCargada.portada = false
      }

      // Cargar imagen kroki
      try {
        this.krokiSrc = require('@/assets/historias/santo_cristo/kroki.png')
        this.imagenCargada.kroki = true
      } catch (e) {
        console.log('No se pudo cargar kroki.png')
        this.krokiSrc = ''
        this.imagenCargada.kroki = false
      }
    },

    volver() {
      this.$emit('cambiar-pantalla', 'mapa')
    },

    volverAHistoria() {
      this.scrollToSeccion('portada')
    },

    irARuta() {
      // Abrir en nueva pestaa
      window.open('https://amonokpy.github.io/', '_blank')
    },

    configurarScroll() {
      const container = this.$refs.scrollContainer
      if (container) {
        container.addEventListener('scroll', this.actualizarSeccionActual)
      }
    },

    actualizarSeccionActual() {
      const secciones = ['portada', 'puzzle', 'final']
      
      for (let seccion of secciones) {
        const elemento = document.getElementById(seccion)
        if (elemento) {
          const rect = elemento.getBoundingClientRect()
          if (rect.top >= 0 && rect.top < window.innerHeight / 2) {
            this.seccionActual = seccion
            break
          }
        }
      }
    },

    scrollToSeccion(seccionId) {
      const elemento = document.getElementById(seccionId)
      if (elemento) {
        elemento.scrollIntoView({ behavior: 'smooth' })
      }
    },

    inicializarAudio() {
      try {
        this.audio = new Audio(require('@/assets/historias/santo_cristo/audio_santo_cristo.mp3'))
        
        this.audio.addEventListener('loadedmetadata', () => {
          this.duracionAudio = this.formatearTiempo(this.audio.duration)
        })

        this.audio.addEventListener('timeupdate', () => {
          if (this.audio) {
            this.tiempoActual = this.formatearTiempo(this.audio.currentTime)
            this.progresoAudio = (this.audio.currentTime / this.audio.duration) * 100
          }
        })

        this.audio.addEventListener('ended', () => {
          this.reproduciendo = false
          this.progresoAudio = 0
          this.tiempoActual = '0:00'
        })
      } catch (error) {
        console.log('Audio no disponible, usando simulacin')
        this.duracionAudio = '5:00'
      }
    },

    formatearTiempo(segundos) {
      const min = Math.floor(segundos / 60)
      const seg = Math.floor(segundos % 60)
      return `${min}:${seg.toString().padStart(2, '0')}`
    },

    toggleReproduccion() {
      if (this.reproduciendo) {
        this.pausarAudio()
      } else {
        this.comenzarAudio()
      }
    },

    comenzarAudio() {
      this.reproduciendo = true
      if (this.audio) {
        this.audio.play()
      } else {
        // Simular reproduccin
        let tiempoSimulado = 0
        const intervalo = setInterval(() => {
          if (!this.reproduciendo) {
            clearInterval(intervalo)
            return
          }
          tiempoSimulado++
          this.tiempoActual = this.formatearTiempo(tiempoSimulado)
          this.progresoAudio = (tiempoSimulado / 300) * 100 // 5 minutos simulado
          
          if (tiempoSimulado >= 300) {
            this.reproduciendo = false
            this.progresoAudio = 0
            this.tiempoActual = '0:00'
            clearInterval(intervalo)
          }
        }, 1000)
      }
    },

    pausarAudio() {
      this.reproduciendo = false
      if (this.audio) {
        this.audio.pause()
      }
    },

    onPuzzleCompletado() {
      this.puzzleCompleto = true
      // Auto-scroll al final despus de completar el puzzle
      setTimeout(() => {
        this.scrollToSeccion('final')
      }, 2000)
    },

    volverAlMapa() {
      this.$emit('cambiar-pantalla', 'mapa')
    }
  }
}
</script>

<style scoped>
.historia-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: #0f0f23;
  color: white;
  font-family: 'Arial', sans-serif;
}

.boton-volver {
  position: fixed;
  top: 20px;
  left: 20px;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  border: 2px solid #fff;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  z-index: 1000;
  transition: all 0.3s ease;
}

.boton-volver:hover {
  background: rgba(255, 255, 255, 0.9);
  color: black;
}

/* Contenedor de scroll */
.contenedor-scroll {
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
}

/* Secciones */
.seccion {
  width: 100%;
  height: 100vh;
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 20px;
}

/* Portada */
.portada {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
}

.contenido-seccion {
  text-align: center;
  max-width: 90%;
  margin: 0 auto;
}

.placeholder-imagen {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  width: 600px;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  border: 4px dashed rgba(255,255,255,0.3);
  margin: 0 auto 30px;
}

.placeholder-imagen p {
  font-size: 24px;
  margin: 10px 0;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.5);
}

.imagen-portada {
  max-width: 600px;
  max-height: 400px;
  object-fit: contain;
  border-radius: 15px;
  box-shadow: 0 15px 35px rgba(0,0,0,0.5);
  margin-bottom: 30px;
}

/* Controles de podcast */
.contenedor-podcast {
  margin: 30px 0;
}

.boton-podcast {
  background: linear-gradient(45deg, #FF6B6B, #4ECDC4);
  color: white;
  border: none;
  border-radius: 30px;
  padding: 15px 35px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(0,0,0,0.3);
}

.boton-podcast:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 30px rgba(0,0,0,0.4);
}

.barra-progreso {
  width: 400px;
  height: 8px;
  background: rgba(255,255,255,0.2);
  border-radius: 4px;
  margin: 15px auto;
  position: relative;
  overflow: hidden;
}

.progreso {
  height: 100%;
  background: linear-gradient(90deg, #4ECDC4, #FF6B6B);
  border-radius: 4px;
  transition: width 0.3s ease;
}

.tiempo-actual {
  font-size: 14px;
  margin-top: 5px;
  color: #ccc;
}

/* Flecha para bajar */
.flecha-bajar {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(255,255,255,0.1);
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  animation: flotar 2s infinite;
  backdrop-filter: blur(10px);
}

@keyframes flotar {
  0%, 100% { transform: translateX(-50%) translateY(0); }
  50% { transform: translateX(-50%) translateY(-10px); }
}

/* Puzzle - PANTALLA COMPLETA */
.puzzle.pantalla-completa {
  background: #000;
  padding: 0;
  justify-content: flex-start;
}

.contenedor-puzzle-completo {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.controles-puzzle {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.7);
  padding: 15px 25px;
  border-radius: 10px;
  text-align: center;
  z-index: 10;
  backdrop-filter: blur(10px);
}

.boton-ver-final {
  background: linear-gradient(45deg, #a29bfe, #fd79a8);
  color: white;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.boton-ver-final:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(0,0,0,0.3);
}

/* Final - CON IMAGEN Y BOTONES */
.final {
  background: linear-gradient(135deg, #0c2461 0%, #1e3799 30%, #4a69bd 100%);
  opacity: 0.7;
  transition: opacity 0.5s ease;
}

.final.desbloqueado {
  opacity: 1;
}

.contenido-final {
  text-align: center;
  max-width: 800px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

/* Contenedor de imagen Kroki */
.contenedor-imagen-kroki {
  background: rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.3);
  border: 2px solid rgba(255,255,255,0.1);
}

.imagen-kroki {
  max-width: 100%;
  max-height: 300px;
  object-fit: contain;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.5);
}

.placeholder-kroki {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  border: 3px dashed rgba(255,255,255,0.3);
}

.placeholder-kroki p {
  font-size: 22px;
  margin: 10px 0;
  text-shadow: 1px 1px 3px rgba(0,0,0,0.5);
}

/* Mensaje de recompensa */
.mensaje-recompensa {
  background: rgba(30, 30, 30, 0.9);
  padding: 30px 25px;
  border-radius: 20px;
  border: 2px solid rgba(255,255,255,0.1);
  backdrop-filter: blur(15px);
}

.mensaje-recompensa h2 {
  font-size: 28px;
  margin-bottom: 20px;
  background: linear-gradient(45deg, #ffeaa7, #fab1a0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.instrucciones {
  font-size: 18px;
  margin-bottom: 25px;
  color: #ccc;
  line-height: 1.5;
}

.estado-puzzle {
  margin: 25px 0;
}

.mensaje-bloqueo {
  font-size: 24px;
  color: #ff7675;
  font-weight: bold;
  margin-bottom: 15px;
}

.mensaje-desbloqueado {
  font-size: 24px;
  color: #55efc4;
  font-weight: bold;
  margin-bottom: 15px;
}

.sub-mensaje {
  font-size: 16px;
  color: #ddd;
  margin-bottom: 10px;
}

.mensaje-ganador {
  font-size: 20px;
  color: #ffeaa7;
  font-weight: bold;
  font-style: italic;
  margin-top: 20px;
  text-shadow: 0 0 10px rgba(255,234,167,0.5);
}

/* Contenedor de botones */
.contenedor-botones {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 500px;
  margin: 0 auto;
  width: 100%;
}

.boton-ruta {
  background: linear-gradient(45deg, #00b894, #00cec9);
  color: white;
  border: none;
  border-radius: 25px;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.boton-ruta:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0,0,0,0.4);
}

.boton-volver-mapa {
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 25px;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

.boton-volver-mapa:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0,0,0,0.4);
}

/* Indicador de progreso */
.indicador-progreso {
  position: fixed;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 15px;
  z-index: 1000;
}

.punto {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255,255,255,0.3);
  cursor: pointer;
  transition: all 0.3s ease;
}

.punto.activo {
  background: white;
  transform: scale(1.3);
}

.punto.desbloqueado {
  background: #55efc4;
}

.punto:not(.desbloqueado):not(.activo) {
  cursor: not-allowed;
  opacity: 0.5;
}

/* Responsive */
@media (max-width: 768px) {
  .imagen-portada {
    max-width: 90%;
    max-height: 300px;
  }
  
  .placeholder-imagen {
    width: 90%;
    height: 300px;
  }
  
  .barra-progreso {
    width: 90%;
  }
  
  .contenido-final {
    max-width: 90%;
  }
  
  .imagen-kroki {
    max-height: 250px;
  }
  
  .placeholder-kroki {
    height: 250px;
  }
  
  .mensaje-recompensa h2 {
    font-size: 22px;
  }
  
  .contenedor-botones {
    max-width: 90%;
  }
  
  .boton-ruta,
  .boton-volver-mapa {
    padding: 12px 20px;
    font-size: 16px;
  }
}
</style>