<template>
  <div class="album-container">
    <!-- Botn para regresar -->
    <button class="btn-volver" @click="volverAInicio">
       Volver al Inicio
    </button>
    
    <!-- Ttulo del lbum -->
    <h1 class="titulo-album">lbum</h1>
    <p class="subtitulo-album">Relatos</p>
    
    <!-- Imagen del lbum -->
    <div class="contenedor-imagen">
      <img 
        :src="require('@/assets/album/album_oficial.jpg')" 
        alt="lbum Oficial Susurros Ancestrales"
        class="imagen-album"
      />
      <div class="info-imagen">
        <h3>lbum Oficial Susurros Ancestrales</h3>
        <p>Coleccin completa de fotografas histricas de Lumbral</p>
        <p class="dimensiones">Dimensiones: 1024  768 px</p>
      </div>
    </div>
    
    <!-- Controles de zoom -->
    <div class="controles-zoom">
      <button @click="zoomOut" class="btn-zoom"></button>
      <span class="texto-zoom">{{ Math.round(nivelZoom * 100) }}%</span>
      <button @click="zoomIn" class="btn-zoom">+</button>
      <button @click="resetZoom" class="btn-reset"> Restablecer</button>
    </div>
    
    <!-- Galera adicional (si tienes ms imgenes) -->
    <div v-if="imagenesAdicionales.length > 0" class="galeria">
      <h3>Ms imgenes</h3>
      <div class="galeria-grid">
        <div 
          v-for="(imagen, index) in imagenesAdicionales" 
          :key="index"
          class="miniatura"
          @click="cambiarImagen(imagen)"
        >
          <img :src="require(`@/assets/album/${imagen.archivo}`)" :alt="imagen.titulo" />
          <p>{{ imagen.titulo }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AlbumOficial', // Cambiado a 'AlbumOficial' (mltiples palabras)
  data() {
    return {
      nivelZoom: 1,
      imagenActual: 'album_oficial.jpg', // La foto sigue llamndose igual
      tituloActual: 'lbum Oficial Susurros Ancestrales',
      imagenesAdicionales: []
    }
  },
  methods: {
    volverAInicio() {
      this.$emit('cambiar-pantalla', 'inicio');
    },
    
    zoomIn() {
      if (this.nivelZoom < 3) {
        this.nivelZoom += 0.1;
      }
    },
    
    zoomOut() {
      if (this.nivelZoom > 0.3) {
        this.nivelZoom -= 0.1;
      }
    },
    
    resetZoom() {
      this.nivelZoom = 1;
    },
    
    cambiarImagen(imagen) {
      this.imagenActual = imagen.archivo;
      this.tituloActual = imagen.titulo;
      this.nivelZoom = 1; // Reset zoom al cambiar imagen
    }
  }
}
</script>

<style scoped>
.album-container {
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  overflow-y: auto;
  padding: 20px;
  font-family: 'Arial', sans-serif;
  color: white;
}

.btn-volver {
  position: absolute;
  top: 20px;
  left: 20px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 10px 20px;
  border-radius: 25px;
  cursor: pointer;
  font-size: 14px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  z-index: 100;
}

.btn-volver:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(-5px);
}

.titulo-album {
  text-align: center;
  font-size: 3rem;
  margin-top: 60px;
  margin-bottom: 10px;
  background: linear-gradient(45deg, #ff8a00, #e52e71);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.subtitulo-album {
  text-align: center;
  color: #aaa;
  margin-bottom: 40px;
  font-size: 1.2rem;
}

.contenedor-imagen {
  max-width: 900px;
  margin: 0 auto 40px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.imagen-album {
  width: 100%;
  height: auto;
  border-radius: 10px;
  transform: scale(v-bind(nivelZoom));
  transition: transform 0.3s ease;
  cursor: zoom-in;
  display: block;
  margin: 0 auto;
}

.info-imagen {
  margin-top: 20px;
  text-align: center;
}

.info-imagen h3 {
  font-size: 1.8rem;
  margin-bottom: 10px;
  color: #ff8a00;
}

.info-imagen p {
  color: #ccc;
  margin-bottom: 5px;
}

.dimensiones {
  color: #888;
  font-size: 0.9rem;
}

.controles-zoom {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin: 30px 0;
  background: rgba(0, 0, 0, 0.3);
  padding: 15px;
  border-radius: 10px;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.btn-zoom {
  background: #4CAF50;
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-zoom:hover {
  background: #45a049;
  transform: scale(1.1);
}

.texto-zoom {
  font-size: 1.2rem;
  font-weight: bold;
  min-width: 80px;
  text-align: center;
}

.btn-reset {
  background: #2196F3;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 20px;
}

.btn-reset:hover {
  background: #0b7dda;
}

.galeria {
  max-width: 900px;
  margin: 40px auto;
  padding: 20px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 15px;
}

.galeria h3 {
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 20px;
  color: #4CAF50;
}

.galeria-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 15px;
  margin-top: 20px;
}

.miniatura {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
}

.miniatura:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-5px);
}

.miniatura img {
  width: 100%;
  height: 100px;
  object-fit: cover;
  border-radius: 5px;
  margin-bottom: 10px;
}

.miniatura p {
  font-size: 0.9rem;
  color: #ccc;
  margin: 0;
}

/* Responsive */
@media (max-width: 768px) {
  .titulo-album {
    font-size: 2rem;
    margin-top: 40px;
  }
  
  .contenedor-imagen {
    padding: 10px;
  }
  
  .galeria-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
</style>