<template>
  <div class="misiones-container">
    <!-- Fondo decorativo -->
    <div class="fondo-misiones">
      <div class="particulas"></div>
    </div>
    
    <!-- Botn para volver -->
    <button class="btn-volver" @click="volverAInicio">
       Volver al Inicio
    </button>
    
    <!-- Cabecera -->
    <div class="cabecera-misiones">
      <h1> Misiones</h1>
      <p class="subtitulo">Completa desafos para desbloquear recompensas exclusivas</p>
      
      <!-- Progreso general -->
      <div class="progreso-general">
        <div class="info-progreso">
          <span class="icono-progreso"></span>
          <div class="texto-progreso">
            <span class="porcentaje">{{ calcularPorcentajeCompletado() }}% completado</span>
            <span class="contador">{{ misionesCompletadas.length }} de {{ misiones.length }} misiones</span>
          </div>
        </div>
        <div class="barra-progreso">
          <div 
            class="relleno-progreso" 
            :style="{ width: calcularPorcentajeCompletado() + '%' }"
          ></div>
        </div>
      </div>
    </div>
    
    <!-- Categoras de misiones -->
    <div class="categorias-misiones">
      <div 
        v-for="(categoria, index) in categorias" 
        :key="index"
        class="categoria"
        :class="{ activa: categoriaActiva === index }"
        @click="categoriaActiva = index"
      >
        <span class="icono-categoria">{{ categoria.icono }}</span>
        <span class="nombre-categoria">{{ categoria.nombre }}</span>
        <span class="contador-categoria">{{ contarMisionesPorCategoria(categoria.tipo) }}</span>
      </div>
    </div>
    
    <!-- Lista de misiones -->
    <div class="lista-misiones">
      <div 
        v-for="mision in misionesFiltradas" 
        :key="mision.id"
        class="card-mision"
        :class="{ 
          'completada': mision.completada,
          'en-progreso': mision.enProgreso && !mision.completada,
          'nueva': mision.nueva && !mision.completada
        }"
      >
        <!-- Estado de la misin -->
        <div class="estado-mision">
          <div v-if="mision.completada" class="badge-completada">
            <span class="icono-check"></span>
            COMPLETADA
          </div>
          <div v-else-if="mision.enProgreso" class="badge-en-progreso">
            <span class="icono-progreso"></span>
            EN PROGRESO
          </div>
          <div v-else-if="mision.nueva" class="badge-nueva">
            <span class="icono-nueva"></span>
            NUEVA
          </div>
        </div>
        
        <!-- Contenido de la misin -->
        <div class="contenido-mision">
          <div class="info-mision">
            <h3 class="titulo-mision">{{ mision.titulo }}</h3>
            <p class="descripcion-mision">{{ mision.descripcion }}</p>
            
            <!-- Requisitos -->
            <div class="requisitos">
              <h4>Requisitos:</h4>
              <ul>
                <li 
                  v-for="(requisito, index) in mision.requisitos" 
                  :key="index"
                  :class="{ 'completado': requisitoCompletado(mision, index) }"
                >
                  <span class="icono-requisito">
                    {{ requisitoCompletado(mision, index) ? '' : '' }}
                  </span>
                  {{ requisito }}
                </li>
              </ul>
            </div>
            
            <!-- Progreso especfico -->
            <div v-if="mision.progreso && !mision.completada" class="progreso-mision">
              <div class="texto-progreso-mision">
                <span>Progreso: {{ mision.progreso.actual }}/{{ mision.progreso.total }}</span>
              </div>
              <div class="barra-progreso-mision">
                <div 
                  class="relleno-progreso-mision" 
                  :style="{ width: calcularProgresoMision(mision) + '%' }"
                ></div>
              </div>
            </div>
          </div>
          
          <!-- Panel lateral -->
          <div class="panel-lateral">
            <!-- Recompensas -->
            <div class="recompensas">
              <h4>Recompensas:</h4>
              <div class="lista-recompensas">
                <div 
                  v-for="(recompensa, index) in mision.recompensas" 
                  :key="index"
                  class="recompensa"
                >
                  <span class="icono-recompensa">{{ recompensa.icono }}</span>
                  <span class="texto-recompensa">{{ recompensa.texto }}</span>
                </div>
              </div>
            </div>
            
            <!-- Botones de accin -->
            <div class="acciones-mision">
              <button 
                v-if="!mision.completada && mision.enProgreso && puedeCompletar(mision)"
                class="btn-completar"
                @click="completarMision(mision)"
              >
                COMPLETAR MISIN
              </button>
              
              <button 
                v-else-if="!mision.completada && !mision.enProgreso"
                class="btn-empezar"
                @click="empezarMision(mision)"
              >
                EMPEZAR MISIN
              </button>
              
              <button 
                v-else-if="mision.completada"
                class="btn-reclamada"
                disabled
              >
                RECLAMADA 
              </button>
              
              <!-- Botones especficos para cada misin -->
              <div v-if="mision.accionesEspecificas && !mision.completada" class="acciones-especificas">
                <button 
                  v-if="mision.id === 1"
                  class="btn-accion-especifica"
                  @click="irAPuzzle"
                >
                  IR AL PUZZLE
                </button>
                
                <button 
                  v-if="mision.id === 2"
                  class="btn-accion-especifica"
                  @click="abrirPodcast"
                >
                  ESCUCHAR PODCAST
                </button>
                
                <button 
                  v-if="mision.id === 3"
                  class="btn-accion-especifica"
                  @click="irAHistoria"
                >
                  VER HISTORIA
                </button>
              </div>
            </div>
            
            <!-- Informacin adicional -->
            <div class="info-adicional">
              <div class="dificultad">
                <span class="icono-dificultad"></span>
                <span class="texto-dificultad">Dificultad: {{ mision.dificultad }}</span>
              </div>
              <div class="tiempo-estimado">
                <span class="icono-tiempo"></span>
                <span class="texto-tiempo">{{ mision.tiempoEstimado }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Estadsticas -->
    <div class="estadisticas">
      <h2> Tu Progreso</h2>
      <div class="grid-estadisticas">
        <div class="estadistica">
          <div class="valor">{{ misionesCompletadas.length }}</div>
          <div class="etiqueta">Misiones Completadas</div>
        </div>
        <div class="estadistica">
          <div class="valor">{{ calcularRecompensasTotales() }}</div>
          <div class="etiqueta">Recompensas Obtenidas</div>
        </div>
        <div class="estadistica">
          <div class="valor">{{ calcularPuntosTotales() }}</div>
          <div class="etiqueta">Puntos Totales</div>
        </div>
        <div class="estadistica">
          <div class="valor">{{ calcularTiempoTotal() }}</div>
          <div class="etiqueta">Horas de Juego</div>
        </div>
      </div>
    </div>
    
    <!-- Logros -->
    <div class="logros">
      <h2> Logros Desbloqueados</h2>
      <div class="grid-logros">
        <div 
          v-for="(logro, index) in logros" 
          :key="index"
          class="card-logro"
          :class="{ 'desbloqueado': logro.desbloqueado }"
        >
          <div class="icono-logro">{{ logro.icono }}</div>
          <div class="info-logro">
            <h4>{{ logro.nombre }}</h4>
            <p>{{ logro.descripcion }}</p>
            <div class="progreso-logro" v-if="!logro.desbloqueado">
              <div 
                class="relleno-logro" 
                :style="{ width: logro.progreso + '%' }"
              ></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Notificaciones -->
    <div v-if="notificacionVisible" class="notificacion" :class="notificacionTipo">
      {{ notificacionMensaje }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'MisionesJuego',
  data() {
    return {
      categoriaActiva: 0,
      notificacionVisible: false,
      notificacionMensaje: '',
      notificacionTipo: 'exito',
      
      categorias: [
        { nombre: 'Todas', icono: '', tipo: 'todas' },
        { nombre: 'Historia', icono: '', tipo: 'historia' },
        { nombre: 'Audio', icono: '', tipo: 'audio' },
        { nombre: 'Puzzles', icono: '', tipo: 'puzzle' },
        { nombre: 'Coleccin', icono: '', tipo: 'coleccion' }
      ],
      
      misiones: [
        {
          id: 1,
          titulo: 'El Puzzle de Lumbral',
          descripcion: 'Completa el puzzle interactivo para descubrir secretos ocultos',
          tipo: 'puzzle',
          dificultad: 'Media',
          tiempoEstimado: '15-20 minutos',
          completada: false,
          enProgreso: true,
          nueva: true,
          progreso: {
            actual: 2,
            total: 5
          },
          requisitos: [
            'Resolver el puzzle del Santo Cristo de Ubat',
            'Encontrar todas las piezas ocultas',
            'Completar en menos de 30 minutos',
            'Descubrir el mensaje secreto'
          ],
          recompensas: [
            { icono: '', texto: '+500 monedas' },
            { icono: '', texto: 'Logro "Puzzle Master"' },
            { icono: '', texto: 'rea secreta desbloqueada' },
            { icono: '', texto: 'Fondo exclusivo' }
          ],
          requisitosCompletados: [true, false, false, false]
        },
        {
          id: 2,
          titulo: 'Voces del Pasado',
          descripcion: 'Escucha el podcast completo del Santo Cristo de Ubat',
          tipo: 'audio',
          dificultad: 'Fcil',
          tiempoEstimado: '25 minutos',
          completada: false,
          enProgreso: false,
          nueva: true,
          requisitos: [
            'Escuchar el podcast completo (100%)',
            'Tomar notas de 3 puntos clave',
            'Compartir en redes sociales',
            'Dejar un comentario'
          ],
          recompensas: [
            { icono: '', texto: '+300 monedas' },
            { icono: '', texto: 'Logro "Odo Histrico"' },
            { icono: '', texto: 'Gua de estudio exclusiva' },
            { icono: '', texto: 'Puntos de experiencia' }
          ],
          requisitosCompletados: [false, false, false, false]
        },
        {
          id: 3,
          titulo: 'Final Revelado',
          descripcion: 'Desbloquea el final completo de la historia del Santo Cristo',
          tipo: 'historia',
          dificultad: 'Alta',
          tiempoEstimado: '45-60 minutos',
          completada: false,
          enProgreso: false,
          nueva: true,
          requisitos: [
            'Completar todas las misiones anteriores',
            'Alcanzar nivel 5 en la historia',
            'Tomar la decisin final correcta',
            'Ver los crditos completos'
          ],
          recompensas: [
            { icono: '', texto: '+1000 monedas' },
            { icono: '', texto: 'Logro "Historiador Maestro"' },
            { icono: '', texto: 'Escena exclusiva desbloqueada' },
            { icono: '', texto: 'Certificado digital' }
          ],
          requisitosCompletados: [false, false, false, false]
        },
        {
          id: 4,
          titulo: 'Coleccionista de Reliquias',
          descripcion: 'Encuentra todas las reliquias histricas escondidas',
          tipo: 'coleccion',
          dificultad: 'Media',
          tiempoEstimado: '30-40 minutos',
          completada: true,
          enProgreso: false,
          nueva: false,
          requisitos: [
            'Encontrar 10 reliquias escondidas',
            'Catalogar cada reliquia',
            'Completar el lbum de coleccin',
            'Compartir coleccin completa'
          ],
          recompensas: [
            { icono: '', texto: '+750 monedas' },
            { icono: '', texto: 'Logro "Coleccionista"' },
            { icono: '', texto: 'Lupa especial desbloqueada' },
            { icono: '', texto: 'Modo foto mejorado' }
          ],
          requisitosCompletados: [true, true, true, true]
        },
        {
          id: 5,
          titulo: 'Explorador del Mapa',
          descripcion: 'Visita todos los puntos de inters en el mapa de Lumbral',
          tipo: 'coleccion',
          dificultad: 'Media',
          tiempoEstimado: '35 minutos',
          completada: false,
          enProgreso: true,
          nueva: false,
          progreso: {
            actual: 6,
            total: 10
          },
          requisitos: [
            'Visitar 10 puntos de inters',
            'Tomar foto en cada ubicacin',
            'Leer todas las descripciones',
            'Completar el recorrido guiado'
          ],
          recompensas: [
            { icono: '', texto: '+600 monedas' },
            { icono: '', texto: 'Logro "Explorador"' },
            { icono: '', texto: 'Mapa mejorado' },
            { icono: '', texto: 'Velocidad de movimiento +10%' }
          ],
          requisitosCompletados: [true, true, false, false]
        }
      ],
      
      logros: [
        {
          id: 1,
          nombre: 'Iniciador de Leyendas',
          descripcion: 'Completa tu primera misin',
          icono: '',
          desbloqueado: true,
          progreso: 100
        },
        {
          id: 2,
          nombre: 'Oidor Atento',
          descripcion: 'Escucha 5 podcasts completos',
          icono: '',
          desbloqueado: false,
          progreso: 40
        },
        {
          id: 3,
          nombre: 'Resolver de Misterios',
          descripcion: 'Completa 3 puzzles diferentes',
          icono: '',
          desbloqueado: false,
          progreso: 33
        },
        {
          id: 4,
          nombre: 'Historiador Consagrado',
          descripcion: 'Termina todas las historias principales',
          icono: '',
          desbloqueado: false,
          progreso: 20
        }
      ]
    };
  },
  computed: {
    misionesFiltradas() {
      const categoria = this.categorias[this.categoriaActiva];
      if (categoria.tipo === 'todas') {
        return this.misiones;
      }
      return this.misiones.filter(m => m.tipo === categoria.tipo);
    },
    
    misionesCompletadas() {
      return this.misiones.filter(m => m.completada);
    },
    
    misionesEnProgreso() {
      return this.misiones.filter(m => m.enProgreso && !m.completada);
    }
  },
  methods: {
    volverAInicio() {
      this.$emit('cambiar-pantalla', 'inicio');
    },
    
    calcularPorcentajeCompletado() {
      const completadas = this.misiones.filter(m => m.completada).length;
      return Math.round((completadas / this.misiones.length) * 100);
    },
    
    contarMisionesPorCategoria(tipo) {
      if (tipo === 'todas') return this.misiones.length;
      return this.misiones.filter(m => m.tipo === tipo).length;
    },
    
    requisitoCompletado(mision, index) {
      return mision.requisitosCompletados[index];
    },
    
    calcularProgresoMision(mision) {
      if (!mision.progreso) return 0;
      return Math.round((mision.progreso.actual / mision.progreso.total) * 100);
    },
    
    puedeCompletar(mision) {
      return mision.requisitosCompletados.every(requisito => requisito);
    },
    
    empezarMision(mision) {
      mision.enProgreso = true;
      mision.nueva = false;
      
      this.mostrarNotificacion(
        `Has empezado la misin "${mision.titulo}"!`,
        'info'
      );
      
      // Guardar estado
      this.guardarEstadoMisiones();
    },
    
    completarMision(mision) {
      if (!this.puedeCompletar(mision)) {
        this.mostrarNotificacion(
          'No has completado todos los requisitos',
          'error'
        );
        return;
      }
      
      mision.completada = true;
      mision.enProgreso = false;
      
      // Aplicar recompensas
      this.aplicarRecompensas(mision);
      
      this.mostrarNotificacion(
        `Misin completada! Recompensas obtenidas`,
        'exito'
      );
      
      // Guardar estado
      this.guardarEstadoMisiones();
    },
    
    aplicarRecompensas(mision) {
      console.log('Recompensas aplicadas para:', mision.titulo);
      // Aqu podras implementar lgica para dar monedas, desbloquear logros, etc.
      
      // Actualizar logros si corresponde
      this.actualizarLogros();
    },
    
    actualizarLogros() {
      // Actualizar progreso de logros basado en misiones completadas
      const completadas = this.misionesCompletadas.length;
      
      // Logro 1: Primera misin
      if (completadas >= 1 && !this.logros[0].desbloqueado) {
        this.logros[0].desbloqueado = true;
        this.logros[0].progreso = 100;
      }
      
      // Otros logros pueden actualizarse aqu
    },
    
    // Acciones especficas para cada misin
    irAPuzzle() {
      this.$emit('cambiar-pantalla', 'historia_santo_cristo');
      this.mostrarNotificacion('Redirigiendo al puzzle...', 'info');
    },
    
    abrirPodcast() {
      // Abrir el podcast de Santo Cristo de Ubat
      window.open('https://open.spotify.com/show/example-santo-cristo-ubate', '_blank');
      
      // Actualizar progreso de la misin 2
      const misionPodcast = this.misiones.find(m => m.id === 2);
      if (misionPodcast && !misionPodcast.completada) {
        misionPodcast.requisitosCompletados[0] = true;
        if (!misionPodcast.enProgreso) {
          misionPodcast.enProgreso = true;
          misionPodcast.nueva = false;
        }
        this.mostrarNotificacion('Podcast abierto! Escchalo completo para progresar', 'info');
      }
      
      this.guardarEstadoMisiones();
    },
    
    irAHistoria() {
      this.$emit('cambiar-pantalla', 'historia_santo_cristo');
      this.mostrarNotificacion('Redirigiendo a la historia...', 'info');
    },
    
    calcularRecompensasTotales() {
      const recompensasPorMision = this.misionesCompletadas.length * 4;
      return recompensasPorMision + 5; // +5 por logros adicionales
    },
    
    calcularPuntosTotales() {
      let puntos = 0;
      this.misionesCompletadas.forEach(mision => {
        if (mision.dificultad === 'Fcil') puntos += 100;
        else if (mision.dificultad === 'Media') puntos += 250;
        else if (mision.dificultad === 'Alta') puntos += 500;
      });
      return puntos;
    },
    
    calcularTiempoTotal() {
      let horas = 0;
      this.misionesCompletadas.forEach(mision => {
        const match = mision.tiempoEstimado.match(/(\d+)/);
        if (match) {
          horas += parseInt(match[1]);
        }
      });
      return horas;
    },
    
    mostrarNotificacion(mensaje, tipo = 'exito') {
      this.notificacionMensaje = mensaje;
      this.notificacionTipo = tipo;
      this.notificacionVisible = true;
      
      setTimeout(() => {
        this.notificacionVisible = false;
      }, 3000);
    },
    
    guardarEstadoMisiones() {
      const estado = {
        misiones: this.misiones,
        logros: this.logros,
        fecha: new Date().toISOString()
      };
      localStorage.setItem('estadoMisiones', JSON.stringify(estado));
    },
    
    cargarEstadoMisiones() {
      const estadoGuardado = localStorage.getItem('estadoMisiones');
      if (estadoGuardado) {
        const estado = JSON.parse(estadoGuardado);
        this.misiones = estado.misiones;
        this.logros = estado.logros;
      }
    }
  },
  mounted() {
    this.cargarEstadoMisiones();
  }
};
</script>

<style scoped>
.misiones-container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #0a3d62 0%, #1e5b8a 50%, #2c8fb3 100%);
  overflow-x: hidden;
  font-family: 'Arial', sans-serif;
  color: white;
  padding-bottom: 50px;
}

.fondo-misiones {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 10% 20%, rgba(255, 255, 255, 0.05) 0%, transparent 20%),
    radial-gradient(circle at 90% 80%, rgba(255, 255, 255, 0.03) 0%, transparent 20%);
  z-index: 1;
}

.btn-volver {
  position: fixed;
  top: 20px;
  left: 20px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 10px 20px;
  border-radius: 25px;
  cursor: pointer;
  font-size: 14px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  z-index: 100;
}

.btn-volver:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(-5px);
}

.cabecera-misiones {
  text-align: center;
  padding: 100px 20px 30px;
  position: relative;
  z-index: 2;
}

.cabecera-misiones h1 {
  font-size: 3.5rem;
  margin-bottom: 10px;
  background: linear-gradient(45deg, #f6d365, #fda085);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.subtitulo {
  color: #a8e6cf;
  font-size: 1.2rem;
  opacity: 0.9;
  margin-bottom: 30px;
}

.progreso-general {
  max-width: 600px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.3);
  padding: 20px;
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.info-progreso {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
}

.icono-progreso {
  font-size: 2rem;
}

.texto-progreso {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.porcentaje {
  font-size: 1.5rem;
  font-weight: bold;
  color: #fdcb6e;
}

.contador {
  font-size: 0.9rem;
  opacity: 0.8;
}

.barra-progreso {
  width: 100%;
  height: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  overflow: hidden;
}

.relleno-progreso {
  height: 100%;
  background: linear-gradient(90deg, #4cd964, #5ac8fa);
  border-radius: 5px;
  transition: width 0.5s ease;
}

/* Categoras */
.categorias-misiones {
  display: flex;
  justify-content: center;
  gap: 15px;
  padding: 0 20px 30px;
  flex-wrap: wrap;
  position: relative;
  z-index: 2;
