<template>
  <div class="viewport-adaptativo">
    <!-- Contenedor que escala todo el juego proporcionalmente -->
    <div 
      class="contenedor-juego-completo"
      :style="{
        transform: `scale(${escalaActual})`,
        width: `${anchoOriginal}px`,
        height: `${altoOriginal}px`
      }"
    >
      <!-- Tu juego completo aqu (1920x1080) -->
      <div class="juego-interno">
        <!-- Fondo -->
        <img 
          :src="require('@/assets/inicio/Fondo.png')" 
          class="fondo-inicio"
          alt="Fondo Inicio"
        />

        <!-- Elementos FIJOS (sin controles de edicin) -->
        <div
          v-for="(elemento, index) in elementos"
          :key="index"
          class="elemento-fijo"
          :style="{
            left: elemento.x + 'px',
            top: elemento.y + 'px',
            width: elemento.ancho + 'px',
            transform: `scale(${elemento.escala})`
          }"
          @click="ejecutarAccion(elemento.accion)"
        >
          <img 
            :src="require(`@/assets/inicio/${elemento.imagen}`)"
            class="imagen-elemento"
            :alt="elemento.nombre"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PantallaInicioFija',
  data() {
    return {
      // Dimensiones ORIGINALES del juego (NO CAMBIAN)
      anchoOriginal: 1920,
      altoOriginal: 1080,
      
      // Escala calculada dinmicamente
      escalaActual: 1,
      
      // Elementos FIJOS - cargados desde localStorage o valores por defecto
      elementos: [
        // Valores por defecto
        { nombre: 'Jugar', imagen: 'boton_jugar.png', accion: 'jugar', x: 1071, y: 690, ancho: 352, escala: 1 },
        { nombre: 'lbum', imagen: 'boton_album.png', accion: 'abrirAlbum', x: 1106, y: 155, ancho: 306, escala: 1 },
        { nombre: 'Misiones', imagen: 'boton_misiones.png', accion: 'abrirMisiones', x: 1103, y: 288, ancho: 313, escala: 1 },
        { nombre: 'Mercado', imagen: 'boton_mercado.png', accion: 'abrirMercado', x: 1093, y: 441, ancho: 313, escala: 1 },
        { nombre: 'Eventos', imagen: 'boton_eventos.png', accion: 'abrirEventos', x: 425, y: 730, ancho: 458, escala: 1 },
        { nombre: 'Facebook', imagen: 'icono_facebook.png', accion: 'abrirFacebook', x: 133, y: 734, ancho: 62, escala: 1 },
        { nombre: 'Instagram', imagen: 'icono_instagram.png', accion: 'abrirInstagram', x: 36, y: 733, ancho: 62, escala: 1 },
        { nombre: 'Sonido', imagen: 'sonido.png', accion: 'toggleSonido', x: 1776, y: 50, ancho: 60, escala: 1 }
      ],
      
      // Estado del sonido
      sonidoActivo: true,
      
      // Para recalcular en resize
      resizeObserver: null
    }
  },
  mounted() {
    // Cargar diseo guardado (si existe)
    this.cargarDisenoGuardado();
    
    // Cargar estado del sonido
    const sonidoGuardado = localStorage.getItem('sonidoActivo');
    if (sonidoGuardado !== null) {
      this.sonidoActivo = JSON.parse(sonidoGuardado);
    }
    this.actualizarIconoSonido();
    
    // Calcular escala inicial
    this.calcularEscala();
    
    // Escuchar cambios de tamao
    window.addEventListener('resize', this.calcularEscala);
    
    // Tambin observar cambios en el contenedor padre
    this.observarCambiosTamao();
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.calcularEscala);
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }
  },
  methods: {
    // ========== SISTEMA DE ESCALADO ==========
    calcularEscala() {
      const contenedor = document.querySelector('.viewport-adaptativo');
      if (!contenedor) return;
      
      const anchoDisponible = contenedor.clientWidth;
      const altoDisponible = contenedor.clientHeight;
      
      // Calcular escala basada en el espacio disponible
      const escalaAncho = anchoDisponible / this.anchoOriginal;
      const escalaAlto = altoDisponible / this.altoOriginal;
      
      // Usar la escala MS PEQUEA para que TODO quepa
      this.escalaActual = Math.min(escalaAncho, escalaAlto);
      
      // Limitar escala mnima (opcional, para evitar que sea muy pequeo)
      if (this.escalaActual < 0.2) {
        this.escalaActual = 0.2;
      }
    },
    
    observarCambiosTamao() {
      // Usar MutationObserver para detectar cambios en dimensiones
      this.resizeObserver = new ResizeObserver(() => {
        this.calcularEscala();
      });
      
      const contenedor = document.querySelector('.viewport-adaptativo');
      if (contenedor) {
        this.resizeObserver.observe(contenedor);
      }
    },
    
    // ========== ACCIONES DE LOS BOTONES ==========
    ejecutarAccion(accion) {
      console.log(`Ejecutando accin: ${accion}`);
      
      const acciones = {
        jugar: () => {
          console.log('Cambiando a pantalla: mapa');
          this.$emit('cambiar-pantalla', 'mapa');
        },
        abrirAlbum: () => {
          console.log('Cambiando a pantalla: album');
          this.$emit('cambiar-pantalla', 'album');
        },
        abrirMisiones: () => {
          console.log('Cambiando a pantalla: misiones');
          this.$emit('cambiar-pantalla', 'misiones');
        },
        abrirMercado: () => {
          console.log('Cambiando a pantalla: mercado');
          this.$emit('cambiar-pantalla', 'mercado');
        },
        abrirEventos: () => {
          alert(' Eventos - Prximamente');
        },
        abrirFacebook: () => {
          alert(' Facebook - Prximamente');
        },
        abrirInstagram: () => {
          console.log('Abriendo Instagram...');
          window.open('https://instagram.com/susurrosancestrales.lumbral', '_blank');
        },
        toggleSonido: () => {
          console.log('Alternando sonido...');
          this.toggleSonido();
        }
      };
      
      if (acciones[accion]) {
        acciones[accion]();
      } else {
        console.error(`Accin no definida: ${accion}`);
      }
    },
    
    toggleSonido() {
      this.sonidoActivo = !this.sonidoActivo;
      localStorage.setItem('sonidoActivo', this.sonidoActivo);
      this.actualizarIconoSonido();
      
      // Aqu podras implementar la lgica real de sonido
      if (this.sonidoActivo) {
        console.log(' Sonido activado');
      } else {
        console.log(' Sonido desactivado');
      }
    },

    actualizarIconoSonido() {
      const indiceSonido = this.elementos.findIndex(e => e.nombre === 'Sonido');
      if (indiceSonido !== -1) {
        // Cambiar la imagen segn el estado del sonido
        this.elementos[indiceSonido].imagen = this.sonidoActivo ? 'sonido.png' : 'sonido_mute.png';
        // Forzar actualizacin reactiva
        this.elementos = [...this.elementos];
      }
    },

    // ========== CARGAR DISEO GUARDADO ==========
    cargarDisenoGuardado() {
      const disenoGuardado = localStorage.getItem('disenoInicio');
      if (disenoGuardado) {
        console.log('Cargando diseo guardado...');
        const elementosCargados = JSON.parse(disenoGuardado);
        
        // Asegurarse de que el botn de sonido exista
        const tieneSonido = elementosCargados.some(e => e.nombre === 'Sonido');
        
        if (!tieneSonido) {
          elementosCargados.push({ 
            nombre: 'Sonido', 
            imagen: this.sonidoActivo ? 'sonido.png' : 'sonido_mute.png', 
            accion: 'toggleSonido', 
            x: 1776, 
            y: 50, 
            ancho: 60, 
            escala: 1 
          });
        }
        
        this.elementos = elementosCargados;
        console.log('Diseo cargado:', this.elementos);
      } else {
        console.log('No hay diseo guardado, usando valores por defecto');
      }
    }
  }
}
</script>

<style scoped>
/* ========== CONTENEDOR PRINCIPAL ========== */
.viewport-adaptativo {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #0a0a0a;
  overflow: hidden;
  position: relative;
  margin: 0;
  padding: 0;
}

/* ========== CONTENEDOR DEL JUEGO COMPLETO ========== */
.contenedor-juego-completo {
  position: relative;
  transform-origin: center center;
  will-change: transform;
  box-shadow: 
    0 0 0 1px rgba(255, 255, 255, 0.1),
    0 20px 60px rgba(0, 0, 0, 0.8);
  border-radius: 4px;
  overflow: visible !important;
}

/* ========== JUEGO INTERNO ========== */
.juego-interno {
  position: relative;
  width: 1920px;
  height: 1080px;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

/* ========== FONDO ========== */
.fondo-inicio {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
  border-radius: 4px;
}

/* ========== ELEMENTOS FIJOS ========== */
.elemento-fijo {
  position: absolute;
  z-index: 2;
  cursor: pointer;
  transition: transform 0.2s ease, filter 0.2s ease;
  /* Asegurar que el clic funcione en toda el rea */
  display: flex;
  justify-content: center;
  align-items: center;
}

.elemento-fijo:hover {
  filter: brightness(1.1) drop-shadow(0 4px 8px rgba(0, 0, 0, 0.6));
  transform: scale(1.05) !important; /* !important para sobrescribir transform inline */
}

.imagen-elemento {
  width: 100%;
  height: auto;
  display: block;
  pointer-events: none; /* Esto es importante para que el clic funcione en el div padre */
}

/* ========== ANIMACIN DE ENTRADA ========== */
@keyframes aparecerSuave {
  from {
    opacity: 0;
    transform: scale(0.98);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.contenedor-juego-completo {
  animation: aparecerSuave 0.4s ease-out;
}

/* ========== EFECTOS HOVER ESPECFICOS ========== */
.elemento-fijo:active {
  transform: scale(0.95) !important;
  transition: transform 0.1s ease;
}

/* Botn de sonido efecto especial */
.elemento-fijo:has(img[src*="sonido"]):hover {
  filter: drop-shadow(0 0 8px rgba(76, 175, 80, 0.3));
}

/* ========== INDICADOR VISUAL DE CLIC ========== */
.elemento-fijo::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: transparent;
  border-radius: inherit;
  transition: background 0.1s ease;
}

.elemento-fijo:active::after {
  background: rgba(255, 255, 255, 0.1);
}

/* ========== GARANTIZAR QUE NUNCA HAYA SCROLL ========== */
.viewport-adaptativo,
.contenedor-juego-completo,
.juego-interno {
  overflow: visible !important;
}

/* ========== RESPONSIVE ADAPTATIVO ========== */
@media (max-width: 1200px) {
  .elemento-fijo:hover {
    transform: scale(1.03) !important;
  }
}

@media (max-width: 768px) {
  .elemento-fijo:hover {
    transform: scale(1.02) !important;
  }
}
</style>