<template>
  <div id="app">
    <PantallaInicio 
      v-if="pantallaActual === 'inicio'" 
      @cambiar-pantalla="cambiarPantalla"
    />
    <MapaJuego
      v-else-if="pantallaActual === 'mapa'"
      @cambiar-pantalla="cambiarPantalla"
    />
    <HistoriaSantoCristo 
      v-else-if="pantallaActual === 'historia_santo_cristo'" 
      @cambiar-pantalla="cambiarPantalla"
    />
    <AlbumOficial 
      v-else-if="pantallaActual === 'album'" 
      @cambiar-pantalla="cambiarPantalla"
    />
    <MercadoJuego 
      v-else-if="pantallaActual === 'mercado'" 
      @cambiar-pantalla="cambiarPantalla"
    />
    <!-- NUEVO: Componente Misiones -->
    <MisionesJuego 
      v-else-if="pantallaActual === 'misiones'" 
      @cambiar-pantalla="cambiarPantalla"
    />
  </div>
</template>

<script>
import PantallaInicio from './components/inicio.vue'
import MapaJuego from './components/mapa.vue'
import HistoriaSantoCristo from './components/historia_santo_cristo.vue'
import AlbumOficial from './components/album.vue'
import MercadoJuego from './components/mercado.vue'
import MisionesJuego from './components/misiones.vue' // NUEVA IMPORTACIN

export default {
  name: 'App',
  components: {
    PantallaInicio,
    MapaJuego,
    HistoriaSantoCristo,
    AlbumOficial,
    MercadoJuego,
    MisionesJuego // NUEVO COMPONENTE
  },
  data() {
    return {
      pantallaActual: 'inicio'
    }
  },
  methods: {
    cambiarPantalla(pantalla) {
      this.pantallaActual = pantalla
    }
  }
}
</script>