<template>
  <div class="viewport-adaptativo">
    <!-- Contenedor que escala todo el juego proporcionalmente -->
    <div 
      class="contenedor-juego-completo"
      :style="{
        transform: `scale(${escalaActual})`,
        width: `${anchoOriginal}px`,
        height: `${altoOriginal}px`
      }"
    >
      <!-- Mapa del juego (1500x800) -->
      <div class="mapa-container">
        <!-- Botn Volver -->
        <button class="boton-volver" @click="volver">
           VOLVER
        </button>

        <!-- Fondo del mapa -->
        <div class="mapa-background">
          <!-- Pasto como fondo base -->
          <img 
            :src="require('@/assets/mapa/fondo_pasto.png')" 
            class="fondo-pasto"
            alt="Pasto"
          />

          <!-- Nubes animadas en los bordes -->
          <div class="nubes-container">
            <img
              v-for="(nube, index) in nubes"
              :key="index"
              :src="nube.src"
              class="nube"
              :style="{
                top: nube.top + 'px',
                left: nube.left + 'px',
                animationDuration: nube.duration + 's',
                animationDelay: nube.delay + 's'
              }"
              alt="Nube"
            />
          </div>

          <!-- Elementos del mapa (solo iglesia y pasto) -->
          <div class="elementos-mapa">
            <!-- IGLESIA SUPER GRANDE - AHORA CLICKEABLE -->
            <div 
              class="iglesia-container elemento-colision"
              :style="{
                left: configIglesia.x + 'px', 
                top: configIglesia.y + 'px',
                width: configIglesia.ancho + 'px',
                height: configIglesia.alto + 'px'
              }"
              @click="entrarIglesia()"
              @mouseenter="hoverIglesia = true"
              @mouseleave="hoverIglesia = false"
            >
              <img 
                :src="require('@/assets/mapa/iglesia.png')" 
                class="iglesia"
                :class="{ 'hover-effect': hoverIglesia }"
                alt="Iglesia"
              />
              <!-- Indicador visual de clic -->
              <div v-if="hoverIglesia" class="indicador-clic">
                <span> HAZ CLIC PARA ENTRAR</span>
              </div>
            </div>
          </div>

          <!-- Personaje -->
          <div 
            class="personaje"
            :style="{
              left: personajePos.x + 'px',
              top: personajePos.y + 'px',
              transform: `scaleX(${personajeDireccion})`
            }"
          >
            <div class="cuerpo"></div>
            <div class="ojos"></div>
          </div>

          <!-- Letrero de espacio cuando est cerca de la iglesia -->
          <div 
            v-if="mostrarLetrero" 
            class="letrero-espacio"
            :style="{ 
              left: (configIglesia.x + configIglesia.ancho / 2 - 40) + 'px', 
              top: (configIglesia.y - 50) + 'px' 
            }"
          >
            <div class="letrero-texto">ESPACIO</div>
            <div class="instruccion">Presiona ENTER</div>
            <div class="instruccion-o">o haz CLIC en la iglesia</div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Indicador de escala (opcional) -->
    <div class="indicador-escala" v-if="mostrarIndicador">
      Escala: {{ Math.round(escalaActual * 100) }}%
    </div>
  </div>
</template>

<script>
export default {
  name: 'MapaJuegoEscalado',
  props: {
    mostrarIndicador: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      // Dimensiones ORIGINALES del mapa (NO CAMBIAN)
      anchoOriginal: 1500,
      altoOriginal: 800,
      
      // Escala calculada dinmicamente
      escalaActual: 1,
      
      // Variables del juego
      personajePos: { x: 100, y: 100 },
      personajeDireccion: 1,
      velocidad: 5,
      mostrarLetrero: false,
      teclas: {
        w: false,
        a: false,
        s: false,
        d: false
      },
      nubes: [],
      elementosColision: [],
      hoverIglesia: false,
      
      // CONFIGURACIN IGLESIA SUPER GRANDE
      configIglesia: {
        x: 450,     // Posicin X (centrada horizontalmente)
        y: 150,     // Posicin Y (ms arriba)
        ancho: 600, // ANCHO SUPER GRANDE
        alto: 500   // ALTO SUPER GRANDE
      },
      
      // Para recalcular en resize
      resizeObserver: null
    }
  },
  mounted() {
    this.inicializarNubes()
    this.inicializarElementosColision()
    this.inicializarControles()
    this.iniciarGameLoop()
    
    // Calcular escala inicial
    this.calcularEscala()
    
    // Escuchar cambios de tamao
    window.addEventListener('resize', this.calcularEscala)
    
    // Tambin observar cambios en el contenedor padre
    this.observarCambiosTamao()
  },
  beforeUnmount() {
    document.removeEventListener('keydown', this.manejarTeclaDown)
    document.removeEventListener('keyup', this.manejarTeclaUp)
    document.removeEventListener('keypress', this.manejarEnter)
    window.removeEventListener('resize', this.calcularEscala)
    if (this.resizeObserver) {
      this.resizeObserver.disconnect()
    }
  },
  methods: {
    // ========== SISTEMA DE ESCALADO ==========
    calcularEscala() {
      const contenedor = document.querySelector('.viewport-adaptativo')
      if (!contenedor) return
      
      const anchoDisponible = contenedor.clientWidth
      const altoDisponible = contenedor.clientHeight
      
      // Calcular escala basada en el espacio disponible
      const escalaAncho = anchoDisponible / this.anchoOriginal
      const escalaAlto = altoDisponible / this.altoOriginal
      
      // Usar la escala MS PEQUEA para que TODO quepa
      this.escalaActual = Math.min(escalaAncho, escalaAlto)
      
      // Limitar escala mnima (opcional, para evitar que sea muy pequeo)
      if (this.escalaActual < 0.2) {
        this.escalaActual = 0.2
      }
    },
    
    observarCambiosTamao() {
      // Usar MutationObserver para detectar cambios en dimensiones
      this.resizeObserver = new ResizeObserver(() => {
        this.calcularEscala()
      })
      
      const contenedor = document.querySelector('.viewport-adaptativo')
      if (contenedor) {
        this.resizeObserver.observe(contenedor)
      }
    },
    
    // ========== MTODOS ORIGINALES DEL JUEGO ==========
    volver() {
      this.$emit('cambiar-pantalla', 'inicio')
    },
    
    inicializarNubes() {
      const tiposNubes = [
        require('@/assets/mapa/nube.png'),
        require('@/assets/mapa/nube2.png'),
        require('@/assets/mapa/nube3.png')
      ]
      
      // Crear nubes alrededor de los bordes
      for (let i = 0; i < 20; i++) {
        this.nubes.push({
          src: tiposNubes[Math.floor(Math.random() * tiposNubes.length)],
          top: Math.random() * 100,
          left: Math.random() * this.anchoOriginal,
          duration: 20 + Math.random() * 30,
          delay: Math.random() * 10
        })
      }
    },
    
    inicializarElementosColision() {
      // Solo la iglesia tiene colisin ahora
      this.elementosColision = [
        { 
          x: this.configIglesia.x, 
          y: this.configIglesia.y, 
          width: this.configIglesia.ancho, 
          height: this.configIglesia.alto - 150
        }
      ]
    },
    
    inicializarControles() {
      document.addEventListener('keydown', this.manejarTeclaDown)
      document.addEventListener('keyup', this.manejarTeclaUp)
      document.addEventListener('keypress', this.manejarEnter)
    },
    
    manejarTeclaDown(event) {
      const tecla = event.key.toLowerCase()
      if (Object.prototype.hasOwnProperty.call(this.teclas, tecla)) {
        this.teclas[tecla] = true
      }
    },
    
    manejarTeclaUp(event) {
      const tecla = event.key.toLowerCase()
      if (Object.prototype.hasOwnProperty.call(this.teclas, tecla)) {
        this.teclas[tecla] = false
      }
    },
    
    manejarEnter(event) {
      if (event.key === 'Enter' && this.mostrarLetrero) {
        this.entrarIglesia()
      }
    },
    
    entrarIglesia() {
      console.log('Entrando a la iglesia...')
      this.$emit('cambiar-pantalla', 'historia_santo_cristo')
    },
    
    iniciarGameLoop() {
      const gameLoop = () => {
        this.actualizarPersonaje()
        this.verificarProximidadIglesia()
        requestAnimationFrame(gameLoop)
      }
      gameLoop()
    },
    
    actualizarPersonaje() {
      const nuevaPos = { ...this.personajePos }
      
      if (this.teclas.w) nuevaPos.y -= this.velocidad
      if (this.teclas.s) nuevaPos.y += this.velocidad
      if (this.teclas.a) {
        nuevaPos.x -= this.velocidad
        this.personajeDireccion = -1
      }
      if (this.teclas.d) {
        nuevaPos.x += this.velocidad
        this.personajeDireccion = 1
      }
      
      // Limitar movimiento dentro del mapa
      nuevaPos.x = Math.max(0, Math.min(this.anchoOriginal - 30, nuevaPos.x))
      nuevaPos.y = Math.max(0, Math.min(this.altoOriginal - 30, nuevaPos.y))
      
      // Verificar colisiones
      if (!this.hayColision(nuevaPos)) {
        this.personajePos = nuevaPos
      }
    },
    
    hayColision(nuevaPos) {
      const radioPersonaje = 15
      
      for (const elemento of this.elementosColision) {
        const distanciaX = Math.abs(nuevaPos.x - (elemento.x + elemento.width / 2))
        const distanciaY = Math.abs(nuevaPos.y - (elemento.y + elemento.height / 2))
        
        if (distanciaX > (elemento.width / 2 + radioPersonaje)) continue
        if (distanciaY > (elemento.height / 2 + radioPersonaje)) continue
        
        if (distanciaX <= (elemento.width / 2)) return true
        if (distanciaY <= (elemento.height / 2)) return true
        
        const dx = distanciaX - elemento.width / 2
        const dy = distanciaY - elemento.height / 2
        if (dx * dx + dy * dy <= (radioPersonaje * radioPersonaje)) return true
      }
      
      return false
    },
    
    verificarProximidadIglesia() {
      const centroIglesiaX = this.configIglesia.x + this.configIglesia.ancho / 2
      const centroIglesiaY = this.configIglesia.y + this.configIglesia.alto / 2
      
      const distancia = Math.sqrt(
        Math.pow(this.personajePos.x - centroIglesiaX, 2) +
        Math.pow(this.personajePos.y - centroIglesiaY, 2)
      )
      
      this.mostrarLetrero = distancia < 150
    }
  }
}
</script>

<style scoped>
/* ========== CONTENEDOR PRINCIPAL ========== */
.viewport-adaptativo {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #0a0a0a;
  overflow: hidden;
  position: relative;
  margin: 0;
  padding: 0;
}

/* ========== CONTENEDOR DEL JUEGO COMPLETO ========== */
.contenedor-juego-completo {
  position: relative;
  transform-origin: center center;
  will-change: transform;
  box-shadow: 
    0 0 0 1px rgba(255, 255, 255, 0.1),
    0 20px 60px rgba(0, 0, 0, 0.8);
  border-radius: 4px;
  overflow: visible !important;
}

/* ========== MAPA CONTAINER ========== */
.mapa-container {
  position: relative;
  width: 1500px;
  height: 800px;
  overflow: hidden;
  background: linear-gradient(to bottom, #87CEEB, #E0F7FA);
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.boton-volver {
  position: absolute;
  top: 20px;
  left: 20px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  border: 2px solid #fff;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  z-index: 100;
  transition: all 0.3s ease;
}

.boton-volver:hover {
  background: rgba(255, 255, 255, 0.9);
  color: black;
}

.mapa-background {
  position: relative;
  width: 100%;
  height: 100%;
}

.fondo-pasto {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.nubes-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 30%;
  pointer-events: none;
}

.nube {
  position: absolute;
  width: 120px;
  height: auto;
  opacity: 0.8;
  animation: moverNube linear infinite;
}

@keyframes moverNube {
  0% { transform: translateX(-10px); }
  50% { transform: translateX(10px); }
  100% { transform: translateX(-10px); }
}

.elementos-mapa {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* ========== IGLESIA CLICKEABLE ========== */
.iglesia-container {
  position: absolute;
  z-index: 2;
  cursor: pointer;
}

.iglesia {
  width: 100%;
  height: 100%;
  object-fit: contain;
  transition: all 0.3s ease;
}

.iglesia.hover-effect {
  filter: brightness(1.1) drop-shadow(0 0 15px rgba(255, 215, 0, 0.5));
  transform: scale(1.02);
}

/* Indicador de clic cuando el mouse est sobre la iglesia */
.indicador-clic {
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.85);
  color: gold;
  padding: 8px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: bold;
  white-space: nowrap;
  z-index: 5;
  border: 2px solid gold;
  animation: brillar 1.5s infinite;
}

.indicador-clic span {
  display: flex;
  align-items: center;
  gap: 5px;
}

@keyframes brillar {
  0%, 100% { 
    box-shadow: 0 0 5px gold;
  }
  50% { 
    box-shadow: 0 0 15px gold, 0 0 20px rgba(255, 215, 0, 0.3);
  }
}

/* ========== PERSONAJE ========== */
.personaje {
  position: absolute;
  width: 30px;
  height: 30px;
  z-index: 3;
  transition: transform 0.2s ease;
}

.cuerpo {
  width: 30px;
  height: 30px;
  background: #FF6B6B;
  border-radius: 50%;
  position: relative;
  box-shadow: 0 2px 5px rgba(0,0,0,0.3);
}

.ojos {
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 8px;
}

.ojos::before,
.ojos::after {
  content: '';
  width: 6px;
  height: 6px;
  background: white;
  border-radius: 50%;
  display: block;
}

/* ========== LETRERO DE ENTRADA ========== */
.letrero-espacio {
  position: absolute;
  background: rgba(0, 0, 0, 0.9);
  color: white;
  padding: 12px 18px;
  border-radius: 12px;
  font-family: 'Arial', sans-serif;
  z-index: 10;
  text-align: center;
  border: 2px solid gold;
  animation: pulsar 1.5s infinite;
  min-width: 140px;
}

.letrero-texto {
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 4px;
  color: gold;
}

.instruccion {
  font-size: 11px;
  opacity: 0.9;
  margin-bottom: 2px;
}

.instruccion-o {
  font-size: 10px;
  opacity: 0.7;
  font-style: italic;
  color: #aaa;
}

@keyframes pulsar {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

/* ========== INDICADOR DE ESCALA ========== */
.indicador-escala {
  position: absolute;
  bottom: 15px;
  right: 15px;
  background: rgba(0, 0, 0, 0.7);
  color: #aaa;
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 11px;
  font-family: monospace;
  z-index: 999;
  backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* ========== ANIMACIN DE ENTRADA ========== */
@keyframes aparecerSuave {
  from {
    opacity: 0;
    transform: scale(0.98);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.contenedor-juego-completo {
  animation: aparecerSuave 0.4s ease-out;
}

/* ========== GARANTIZAR QUE NUNCA HAYA SCROLL ========== */
.viewport-adaptativo,
.contenedor-juego-completo,
.mapa-container {
  overflow: visible !important;
}

/* ========== MEDIA QUERIES ========== */
@media (max-width: 1600px) {
  .boton-volver {
    font-size: 14px;
    padding: 8px 16px;
  }
  
  .letrero-espacio {
    font-size: 90%;
    min-width: 120px;
  }
  
  .indicador-clic {
    font-size: 11px;
    top: -35px;
  }
}

@media (max-width: 1200px) {
  .boton-volver {
    font-size: 13px;
    padding: 7px 14px;
  }
  
  .letrero-espacio {
    font-size: 85%;
    padding: 8px 12px;
    min-width: 110px;
  }
  
  .indicador-clic {
    font-size: 10px;
    top: -30px;
    padding: 6px 10px;
  }
}

@media (max-width: 800px) {
  .boton-volver {
    font-size: 12px;
    padding: 6px 12px;
  }
  
  .personaje {
    width: 25px;
    height: 25px;
  }
  
  .cuerpo {
    width: 25px;
    height: 25px;
  }
  
  .letrero-espacio {
    font-size: 80%;
    padding: 6px 10px;
    min-width: 100px;
  }
  
  .indicador-clic {
    font-size: 9px;
    top: -25px;
    padding: 4px 8px;
  }
}

/* Asegurar que todo sea clickeable incluso cuando est escalado */
.boton-volver,
.iglesia-container,
.letrero-espacio {
  transform-origin: top left;
}
</style>